import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import {Div,Input,FormLayout,FormLayoutGroup,Button,Snackbar,Avatar, View, Panel, PanelHeader, Group, CellButton, HeaderButton, platform, IOS, Tooltip } from '@vkontakte/vkui';
import axios from "axios";
import GetOneAddress from "./GetOneAddress";
import GetOneAddressOff from "./GetOneAddressOff";
import OneProduct from "../catalog/OneProduct";
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';

import FormNewAddress from './FormNewAddress';

class DiliveryAddress extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listAddress:[],
            listAddressOff:[],
            addressOk:false,
            dataAddress:""
        }
    }

    componentDidMount() {
        this.getAddressUserSystem();
        this.props.setStates({formNewAddressData: <FormNewAddress setPopout={this.props.setPopout.bind(this)} userInfo={this.props.userInfo} getAddressUserSystem={this.getAddressUserSystem.bind(this)} setAddressDefault={this.props.setAddressDefault.bind(this)} hideModals={this.props.hideModals.bind(this)} />});
    }

    setAddressEdit(e){
        //console.log(this.state.listAddress);
        this.props.setStates({formEditAddressData: <FormNewAddress AddressEdit={this.state.listAddress[e]} setPopout={this.props.setPopout.bind(this)} userInfo={this.props.userInfo} getAddressUserSystem={this.getAddressUserSystem.bind(this)} setAddressDefault={this.props.setAddressDefault.bind(this)} hideModals={this.props.hideModals.bind(this)} />});
        this.props.showModals('MODAL_EditADDRESS');
    }


    getAddressUserSystem(delUpdate = false){
        axios('https://-./api/getAddressUser&apiKey='+global.ApiKey+'',
            {
                method: 'POST',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                data: {
                    id_UserVK: this.props.userInfo.id,
                },
                responseType:'text'
            })
            .then(({ data }) => {
                if("error" in data){
                    console.log(data.error,'|',data.errorInfo);
                    if(data.error === 'Address not found'){
                        console.log('       ');
                        this.getFormNewAddress();
                    }else{

                    }
                }else{
                   // console.log(data);
                    this.setState({listAddress:data.listAddress});
                    this.props.setStates({listAddressGlobal:data.listAddress});

                    this.setState({listAddressOff:data.ListAddressNoDelivery});
                    this.props.setStates({listAddressGlobalOff:data.ListAddressNoDelivery});

                    if(delUpdate === true){
                        this.getAddressUsers(this.state.listAddress,true);
                    }else{
                        this.getAddressUsers(this.state.listAddress);
                    }
                }
            })
            .catch((err) => {
                console.log("ERROR: --- ---  ",err);
            });
    }


    setAddressInfoUsers(e,type=0){
            if (type === 0) {
                var listAddressU = Object.keys(this.state.listAddress);
                if(listAddressU.length > 0) {
                    // console.log(listAddressU,"  ");

                    // console.log('    ',this.state.listAddress[listAddressU[0]].diliveryData.priceDilivery);

                    var DataSetAddress = this.state.listAddress[listAddressU[e]];
                    this.props.setStates({
                        addressOrderInfoUser: '. ' + DataSetAddress.city + ', . ' + DataSetAddress.street + ', . ' + DataSetAddress.home
                    });
                }
            }
    }

    getFormNewAddress(){
        this.props.setAddressDefault(null);
        this.state.dataAddress = <FormNewAddress setPopout={this.props.setPopout.bind(this)} userInfo={this.props.userInfo} getAddressUserSystem={this.getAddressUserSystem.bind(this)} setAddressDefault={this.props.setAddressDefault.bind(this)}/>;
        this.setState({addressOk:true});
        this.props.setShowBTNNewAddress({showBTNNewAddress:false});

    }

    setDataDiliveryPrice(n,ee = ' 0'){
        var listAddressU = Object.keys(this.state.listAddress);
        // console.log(listAddressU,"  ");

        // console.log('    ',this.state.listAddress[listAddressU[0]].diliveryData.priceDilivery);

        if(listAddressU.length <= 0){
            console.log(' ,      ');
            this.props.setAddressDefault(null);
            //return this.getAddressUsers(this.state.listAddress, true);
        }else {
            if (listAddressU[n] in this.state.listAddress) {
                //console.log(' ,   ');
            } else {
               // console.log('  ,   ');
                this.props.setAddressDefault(0);
               // console.log('     0,  ');
                return this.getAddressUsers(this.state.listAddress, true);
            }
        }

        if(listAddressU.length <= 0){
           // console.log(' ,  ');
           //  console.log('    ', ee);
        }else {
            var DataSetAddress = this.state.listAddress[listAddressU[n]].diliveryData;
            this.props.setStates({
                priceDelivery: DataSetAddress.priceDilivery,
                minSumOrder: DataSetAddress.min_sum_order,
                minSumFreeDelivery: DataSetAddress.min_sum_free_dilivery
            });
           // console.log('    ', ee);
        }

    }

    getAddressUsers(listAddress,delUpdate = false) {
        if(this.props.addressDefault === null & Object.keys(listAddress).length > 0){
          //  console.log('     0','  ',Object.keys(listAddress).length);
            this.props.setAddressDefault(0);
            this.setDataDiliveryPrice(0);
            this.props.setDeliveryDataItogs();
            this.setAddressInfoUsers(this.props.addressDefault);
        }

        if(this.props.addressDefault !== null & Object.keys(listAddress).length <= 0){
           // console.log('     0     ','  ',Object.keys(listAddress).length);
            this.props.setAddressDefault(null);
            this.setDataDiliveryPrice(0);
        }

        if(delUpdate === true){
           // console.log('     0');
            this.props.setAddressDefault(0);
            this.getAddressUsers(listAddress,false);
            this.setDataDiliveryPrice(0);
            this.props.setDeliveryDataItogs();
            this.setAddressInfoUsers(this.props.addressDefault);
        }else{
            this.state.dataAddress = Object.keys(listAddress).map((dataAddress, keyId) =>
                <GetOneAddress
                    setAddressInfoUsers={this.setAddressInfoUsers.bind(this)}
                    setDeliveryDataItogs={this.props.setDeliveryDataItogs.bind(this)}
                    setDataDeliveryPrice={this.setDataDiliveryPrice.bind(this)}
                    getAddressUserSystem={this.getAddressUserSystem.bind(this)}
                    setPopout={this.props.setPopout.bind(this)}
                    userInfo={this.props.userInfo}
                    setAddressEdit={this.setAddressEdit.bind(this)}
                    key={'Address_'+keyId}
                    AddresInfo={listAddress[dataAddress]}
                    NumberAddress={keyId}
                    addressDefault={this.props.addressDefault}
                    setAddressDefault={this.props.setAddressDefault.bind(this)}
                />
            );

            this.state.dataAddressOff = Object.keys(this.state.listAddressOff).map((dataAddress2, keyId2) =>
                <GetOneAddressOff
                    setAddressInfoUsers={this.setAddressInfoUsers.bind(this)}
                    setDeliveryDataItogs={this.props.setDeliveryDataItogs.bind(this)}
                    setDataDeliveryPrice={this.setDataDiliveryPrice.bind(this)}
                    getAddressUserSystem={this.getAddressUserSystem.bind(this)}
                    setPopout={this.props.setPopout.bind(this)}
                    userInfo={this.props.userInfo}
                    setAddressEdit={this.setAddressEdit.bind(this)}
                    key={'Address_'+keyId2}
                    AddresInfo={this.state.listAddressOff[dataAddress2]}
                    NumberAddress={keyId2}
                    addressDefault={this.props.addressDefault}
                    setAddressDefault={this.props.setAddressDefault.bind(this)}
                />
            );
            this.setState({addressOk:true});
            this.props.setShowBTNNewAddress({showBTNNewAddress:true});
            this.setDataDiliveryPrice(this.props.addressDefault, ' '+this.props.addressDefault);
            this.props.setDeliveryDataItogs();
            this.setAddressInfoUsers(this.props.addressDefault);
        }

    }

    getListAddressVisual(){
        return(
            <div>
                {this.state.dataAddress}
                {this.state.dataAddressOff}
            </div>
        );
    }


    render() {
        return (this.state.addressOk === false ? <div>   <ScreenSpinner/></div> : this.getListAddressVisual()
            );
    };
}

export default DiliveryAddress;