import React from 'react';
import { Div } from '@vkontakte/vkui';


export default function ButtonInCart({CartProducts,typeProduct2,setCarts}){

    function getInCarts(typeProduct,setCart){
        // console.log(setCart);
        const Buttons =
            <button onClick={() => {setCart(typeProduct);}}>
                 
            </button>;

        return Buttons;
    }

    function getCountButton(typeProduct,counts,setCart){
        const Buttons =
            <div className="CounterInCart">
                <div className="minus" onClick={() => {setCart(typeProduct,'-');}}>
                    -
                </div>
                {counts}
                <div className="pluse" onClick={() => {setCart(typeProduct);}}>
                    +
                </div>
            </div>;

        return Buttons;
    }

    function getButtonInCartProduct(typeProduct,setCart){
       // console.log(CartProducts,'---0---',typeProduct,setCart);
        if(!!CartProducts){
            var ButOk = false;
            var id_CartPr = '';
            CartProducts.map((data,key)=>{
                //console.log(data.id_Product,typeProduct.id_Product, data.type_product,typeProduct.type_product,'++++++++++++++++++');
                if(data.id_Product === typeProduct.id_Product & data.type_product === typeProduct.type_product){
                    ButOk = true;
                    id_CartPr = key;
                }
            });

            if(ButOk === true){
                //console.log('   ');
                return  getCountButton(typeProduct,CartProducts[id_CartPr]['count'],setCart);
            }else{
                //console.log('   ');
                return getInCarts(typeProduct,setCart);
            }
        }else{
            //console.log('   ,   ');
            return getInCarts(typeProduct,setCart);
        }


    }

    return(getButtonInCartProduct(typeProduct2,setCarts));

}
