import React, { useState, useEffect } from 'react';
import {Div, View, Panel, Separator, PanelHeader, Group, Button, CellButton, HeaderButton, platform, IOS, Tooltip, HorizontalScroll, Placeholder } from '@vkontakte/vkui';
import axios from "axios";
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Icon56MarketOutline from '@vkontakte/icons/dist/56/market_outline';
import Dilivery from "./Dilivery";

import './styleCart.css';

import ButtonCart from "../catalog/ButtonInCart";
import PaymentsPage from "./PaymentsPage";

const osName = platform();


class GetCartHome extends React.Component {

    constructor (props)
    {
        super(props);
        this.state = {
            activePanel:"home",
            popout:null,
            LoadDopProduct:this.props.LoadDopProduct
            //popout:"<ScreenSpinner size='large' />",
        }
    }

    ActivePanels(e){
        this.setState({activePanel: e});
    }

    componentDidMount() {
        async function PopupLoadDop(LoadStatus,setPopout){
            if(LoadStatus === true) {
                setPopout();
            }else {
                setPopout(null);
            }
        }

        PopupLoadDop(this.state.LoadDopProduct,this.setPopout.bind(this));
    }


    ReloadDeliveryPay(){

    }

    GetPresetsVisual(){
        if(Object.keys(this.props.PresetsDopProductList.list).length >0){
            const ListPresets = this.props.PresetsDopProductList.list.map((datas,number)=>{
                return(
                    <div key={number+'PresetsTitle'}>{datas.title} -  {datas.count} {datas.typeCount}</div>
                );
            });
            return(
                <Group
                title="    "
                    style={{textAlign:"center",overflow: "hidden"}}>
                    <img src={this.props.PresetsDopProductList.img}
                         style={{
                             float: "left",
                             width: "35%",
                             marginLeft:"3%",
                             marginRight:"3%"
                         }}
                    />
                    <Div style={{textAlign:"left","lineHeight":"2.1em",paddingRight:"0"}}>{ListPresets}</Div>
                </Group>
            );
        }else{
            //console.log('oo',Object.keys(this.props.PresetsDopProductList.list).length);
            return('');
        }
    }

    DopProductListVisual(){
        //console.log('');
        if(Object.keys(this.props.DopProductList).length > 0) {
            const ListDopProduct = Object.keys(this.props.DopProductList).map(
                (datas,number) => {
                    return (
                        <div className="BlockOneDopProductCart" key={datas}>
                            <div className="imgDopProductBlock">
                                <img src={"//-."+this.props.DopProductList[datas].img} />
                            </div>
                            <div className="titleDopProductBlock">
                                {this.props.DopProductList[datas].title}
                            </div>
                            <div className="priceDopProductBlock WeightFontBold">
                                {this.props.DopProductList[datas].price} 
                            </div>
                            <div className="buttonDopProductBlock">
                                {this.getButtonDopProduct(
                                    this.props.DopProductList[datas].id,
                                    this.props.setDopProduct.bind(this),
                                    this.getCountDopProductIcon(
                                        this.props.DopProductList[datas].id,
                                        this.props.DopProductUserSet
                                    ),
                                    this.props.DopProductList[datas].price
                                )}
                            </div>
                        </div>
                    );
                }
            );

            return (
                <Group title="  ">
                    <HorizontalScroll>
                        <div key="AllDopProductBlock"
                             style={{display:"flex",paddingBottom:"20px"}}
                        >{ListDopProduct}
                            <div style={{display:"block",width:"20px",height:"2px",minWidth:"2px"}}></div>
                        </div>
                        <div className="clr"></div>
                    </HorizontalScroll>

                </Group>
            );
        }else{
            return ("");
        }
    }

    getCountDopProductIcon(Prod,alls){
        //console.log(Prod,alls);
        if(Prod in alls){
            return alls[Prod].count;
        }else{
            return 0;
        }
    }

    getButtonDopProduct(idProduct,setCart,counts = 0,price){
        const Buttons =
            <div className="CounterInCart" style={{float:"none",margin:"auto",display:"block",width:"132px"}}>
                <div className="minus" onClick={() => {
                this.setState({"LoadDopProduct":true});
                    setCart(idProduct,'-',price);
                }}>
                    -
                </div>
                {counts}
                <div className="pluse" onClick={() => {
                    this.setState({"LoadDopProduct":true});
                    setCart(idProduct,'+',price);}
                }>
                    +
                </div>
            </div>;

        return Buttons;
    }

    setPopout(e){
        if(e === ''){
            // useState(<ScreenSpinner size='large' />);
            this.setState({popout: '<ScreenSpinner size=\'large\' />'});
        }else{
            // useState(e);
            this.setState({popout: e});
        }
    }


    render(){
        function setPopout(e){
            if(e === ''){
                // useState(<ScreenSpinner size='large' />);
                this.setState({popout: '<ScreenSpinner size=\'large\' />'});
            }else{
                // useState(e);
                this.setState({popout: e});
            }
        }

       function go(e){
         setActivePanel(e);
         // setActivePanel(e.currentTarget.dataset.to);
       };

       function setActivePanel(e){
           // useState(e);
          // this.setState({activePanel: e});
           this.ActivePanels(e);
       }

       function  getCartVisual(id, Products,setCart){
           //setPopout(null);
            if(Products.length<=0){
                return(<div style={{"textAlign":"center","padding":"5px 10px","marginTop":"30px"}}><h2> </h2><p> ,    - ,       !</p></div>);
            }else {

                var results = Products.map((cartProduct,idProducts) =>
                    <div className="ProductInCartBlock" key={"blockProducts_"+idProducts}>
                        <div

                        className="blockProduct"
                        style={{
                            "width": " 100%",
                            "margin": "5% 0",
                            "padding": "0%",
                            "float":"none",
                            marginBottom:10
                        }}
                        >
                            <div className="imgPrewieProduct">
                                <img src={cartProduct.img}/>
                            </div>
                            <div className="titleProduct">
                                {/*{escapeHtmlDecode(typeProduct.title)}*/}
                                {cartProduct.title} <span className="redTextP">{cartProduct.type_info}</span>
                            </div>
                            <div className="DescrProduct2">
                                {cartProduct.description}
                            </div>
                            <div style={{clear:"both"}}>
                                {/*: {cartProduct.count} <br/>*/}
                                <span className="priceProduct">: {getPriceProducts(cartProduct)}</span>
                                <div className="priceProductBig"style={{width:"100%",bottom:-8}}><ButtonCart CartProducts={Products} typeProduct2={cartProduct}
                                                                             setCarts={setCart.bind(this)}/></div>
                            </div>
                        </div>
                        <Separator style={{ margin: '20px 0' }} />
                    </div>
                );

                return(<div className="CartBlockProducts">{results}</div>);

            }
        }

        function getDiliveryData(){
            return (<div><h2></h2></div>);
        }


        function getPriceProducts(cartProduct){
          // console.log(cartProduct);
            if(!("price_discount" in cartProduct) || cartProduct.price_discount === false){
                return (<span className="WeightFontBold">
                {cartProduct.count * cartProduct.price} 
                </span>
                );
            }else{
                return(
                    <span className="WeightFontBold">
                    <s className="old_price_style" style={{fontWeight:"460",fontSize:"0.9em",marginRight:"5px",color:"#c2c2c2"}}>{cartProduct.count * cartProduct.price_discount.old_price} </s>
                        {cartProduct.count * cartProduct.price_discount.new_price} 
                </span>
                );
            }
        }



        function getButtonNavigation(typeNav, action){

           switch(typeNav){
               case"1": var ButtonBlock = <div className="ButtonPanelNavigation"><button className="activeButons" onClick={()=> {action({activePanel: "home"});}}> </button> <button onClick={()=> {action({activePanel: "dilivery"});}} data-to="dilivery"></button> <button onClick={()=> {action({activePanel: "order"});}}></button></div>;break;
               case"2": var ButtonBlock = <div className="ButtonPanelNavigation"><button onClick={()=> {action({activePanel: "home"});}}> </button> <button className="activeButons" onClick={()=> {action({activePanel: "dilivery"});}} data-to="dilivery"></button> <button onClick={()=> {action({activePanel: "order"});}}></button></div>;break;
               case"3": var ButtonBlock = <div className="ButtonPanelNavigation"><button onClick={()=> {action({activePanel: "home"});}}> </button> <button onClick={()=> {action({activePanel: "dilivery"});}} data-to="dilivery"></button> <button className="activeButons" onClick={()=> {action({activePanel: "order"});}}></button></div>; break;
           }

           return(
               <HorizontalScroll>
                   {ButtonBlock}
               </HorizontalScroll>
           );
        }

        function getOneStepCartNone(){
           return(
               <Panel id="home">

                   <Placeholder
                       icon={<Icon56MarketOutline />}
                       action={false}

                   >
                       <h2> </h2>
                       <p>  ,    - 1 </p>
                   </Placeholder>

               </Panel>
           );
        }

        function getOneStepCart(ProductList,setCart,sumCart,setState,GetPresetsVisual,DopProductList){
           return (
               <Panel id="home" >
               <PanelHeader theme="light"> {getButtonNavigation('1', setState.bind(this))}</PanelHeader>
               <Group title="">
                <Div className="CartBlockProductsOneStep">
                {getCartVisual("homes", ProductList, setCart.bind())}
            <div style={{clear:"both"}}></div>
            </Div>
        </Group>
                   {GetPresetsVisual}
                   {DopProductList}
        <Group>
            <Div className="SumItogOneSTepCart"> : <span className="OrangeColor">{sumCart} </span></Div>
        </Group>
        <Div><Button size="xl" onClick={()=> {setState({activePanel: "dilivery"});}} data-to="dilivery"> </Button></Div>
               </Panel>
        );
        }



        return(
            <View
                activePanel={this.state.activePanel}
                className={
                    osName === IOS ?
                        "IOSCLASSCSS"
                        :
                        "AndroidCLASSCSS"
                }
                popout={this.state.popout}
                header={this.props.ProductList.length<=0
                    ?
                    false
                    :
                    true
                }
            >


                    {this.props.ProductList.length<=0 ? getOneStepCartNone() :

                        getOneStepCart(this.props.ProductList, this.props.setCart.bind(),this.props.sumCart, this.setState.bind(this),this.GetPresetsVisual(),this.DopProductListVisual())
                    }

                <Panel id="dilivery">
                    <PanelHeader theme="light">{getButtonNavigation('2', this.setState.bind(this))}</PanelHeader>
                    <Dilivery
                        userSystemInfo={this.props.userSystemInfo}
                        userInfo={this.props.userInfo}
                        getUserSystemInfo={this.props.getUserSystemInfo.bind()}

                        addressDefault={this.props.addressDefault}
                        setAddressDefault={this.props.setAddressDefault.bind(this)}

                        AffiliateDefault={this.props.AffiliateDefault}
                        setAffiliateDefault={this.props.setAffiliateDefault.bind(this)}

                        checkedTypeDilivery={this.props.checkedTypeDilivery}
                        setTypeDilivery={this.props.setTypeDilivery.bind(this)}

                        commentary={this.props.commentary}
                        setComment={this.props.setComment.bind(this)}

                        countPerson={this.props.countPerson}

                        setStates={this.props.setStates.bind(this)}
                        setDeliveryDataItogs={this.props.setDeliveryDataItogs.bind(this)}
                        getDeliveryDataPriceItogAffiliate={this.props.getDeliveryDataPriceItogAffiliate.bind(this)}

                        /*    */
                        sumCart={this.props.sumCart}

                        setPopout={this.setState.bind(this)}
                        ProductList={this.props.ProductList}

                        showModal={this.props.showModal.bind(this)}
                        hideModals={this.props.hideModals.bind(this)}

                        DataDeliveryInfo={this.props.DataDeliveryInfoPay}

                    />
                    <Div style={{display: 'flex'}}>
                        <Button size="l" stretched level="secondary"  style={{ marginRight: 8 }} className="activeButons" onClick={()=> {this.setState({activePanel: "home"});}}> </Button>
                        <Button size="l" stretched className="activeButons" onClick={()=> {this.setState({activePanel: "order"});}}> </Button>
                    </Div>
                </Panel>
                <Panel id="order">
                    <PanelHeader theme="light">{getButtonNavigation('3', this.setState.bind(this))}</PanelHeader>
                    <PaymentsPage
                        userSystemInfo={this.props.userSystemInfo}
                        userInfo={this.props.userInfo}
                        SalePoints={this.props.SalePoints}
                        setStates={this.props.setStates.bind(this)}
                        sumCart={this.props.sumCart}
                        checkedTypeDilivery={this.props.checkedTypeDilivery}
                        addressDefault={this.props.addressDefault}

                        DataDeliveryInfo={this.props.DataDeliveryInfoPay}

                        getReloadInfODeliveryPay={this.props.checkedTypeDilivery ? this.props.getDeliveryDataPriceItogAffiliate.bind(this) : this.props.setDeliveryDataItogs.bind(this)}


                        action={this.setState.bind(this)}

                        setOkNewOrderView={this.props.setOkNewOrderView.bind(this)}
                    />
                    <Div style={{display: 'flex'}}>
                        <Button size="l" level="secondary" stretched style={{ marginRight: 8 }} className="activeButons" onClick={()=> {this.setState({activePanel: "dilivery"});}} data-to="dilivery"> </Button>

                    </Div>
                </Panel>
            </View>
        );

    };

}

export default GetCartHome;