import React from 'react';

import connect from '@vkontakte/vk-connect';
import axios from "axios";

import {Group, Cell, CellButton} from '@vkontakte/vkui';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Icon24DoNotDisturb from '@vkontakte/icons/dist/24/do_not_disturb';
import Icon24Recent from '@vkontakte/icons/dist/24/recent';
import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';
import Icon24DoneOutline from '@vkontakte/icons/dist/24/done_outline';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';


class UserOrderList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loadOrders:false
        }
    }



    getAllGroups(){
        return(
            <Group>
                <CellButton
                before={<Icon24Recent /> }
                onClick={this.props.setTypeOrderList.bind(this," ","now")}
                >
                    <span style={{color:"#262522"}}> </span>
                </CellButton>
                <CellButton
                before={<Icon24DoneOutline />}
                onClick={this.props.setTypeOrderList.bind(this," ","old")}
                >
                    <span style={{color:"#262522"}}> </span>
                </CellButton>
                <CellButton
                before={<Icon24Cancel />}
                onClick={this.props.setTypeOrderList.bind(this," ","cancel")}
                >
                    <span style={{color:"#262522"}}> </span>
                </CellButton>
            </Group>
        );
    }


    render(){
        return(
            this.state.loadOrders
                ?
                <ScreenSpinner />
                :
                this.getAllGroups()
        );
    }

}

export default UserOrderList;