import React from 'react';

import axios from "axios";

import {Group, Cell} from '@vkontakte/vkui';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';

import Icon24Recent from '@vkontakte/icons/dist/24/recent';
import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';
import Icon24DoneOutline from '@vkontakte/icons/dist/24/done_outline';


class OrderTypeList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listOrderUser:[],
            loadOrders:true
        }
    }

    componentDidMount() {
        this.getOrderUserSystem();
    }

    getOrderUserSystem(){
        this.setState({loadOrders:true});
        axios({
            method:'get',
            url:'https://-./api/getOrdersUserVK&apiKey='+global.ApiKey+'&idUserVK='+this.props.userData.id,
            responseType:'text'
        })
            .then(({data})=>{
                if("error" in data){
                    this.setState({countOrder:0});
                }else{
                    //console.log('   ',data);
                    this.setState({listOrderUser:data.list});
                    this.props.listOrderUserSet(data.list);
                }
                this.setState({loadOrders:false});
            })
            .catch((err) =>{
                console.log('  ',err);
            });
    }


    ReverceList(Pr){
        var res = Object.keys(Pr).reduceRight(function (arr, last, index, coll) {
            // console.log(last, index);
            return (arr = arr.concat(last))
        }, []);
        return res;
    }

    getOrderType(type = 'now'){
        var typeNumber = 0;
        var iconsD ='';
        switch(type){
            case 'now': typeNumber = 0; iconsD = <Icon24Recent /> ;break;
            case 'cancel': typeNumber = 3;  iconsD = <Icon24Dismiss /> ;break;
            case 'old': typeNumber = 1;  iconsD = <Icon24DoneOutline /> ;break;
            default:typeNumber =0;
        }

        //console.log(this.state.listOrderUser)

        var countOrders = 0;

        const ListOrder = this.ReverceList(this.state.listOrderUser).map((dataOrder,keyOrder)=>{
            //console.log(' '+dataOrder,'');
            var CurrentOrder = this.state.listOrderUser[dataOrder];

            var statusY = false;


            if(typeNumber === 1){
                if(CurrentOrder.status_order == typeNumber || CurrentOrder.status_order == 6){
                    statusY = true;
                }else{
                    statusY = false;
                }
            }else{
                if(CurrentOrder.status_order == typeNumber){
                    statusY = true;
                }else{
                    statusY = false;
                }
            }


            if(CurrentOrder.delete_order == '1'){
                statusY = false;
            }

            if(statusY === true){
                countOrders++;
                //console.log(' '+dataOrder,' ');
                var summaOrders = <span>: <b>{CurrentOrder.sum_order}</b></span>;

                return (
                    <Cell key={"Orders_"+dataOrder}
                          before={iconsD}
                          description={<span>{CurrentOrder.date} <i>{CurrentOrder.time}</i> | {summaOrders}</span>}
                          onClick={this.props.setOrderShowNumber.bind(this,CurrentOrder.id_order)}
                    >
                        <b> {CurrentOrder.id_order}</b> <span style={{fontSize:"0.8em",marginLeft:"2%",color:"#6a97fb"}}> ...</span>
                    </Cell>
                );
            }else{
                //console.log(' '+dataOrder,'');
            }

        });

        if(countOrders <=0){
            return (
                <Cell key={"Orders_None"}
                >
                     
                </Cell>
            );
        }

        return ListOrder;

    }


    getNowOrder(){
        return(
            <Group key="ListOrderNow"
                   title=" "
            >
                {this.getOrderType('now')}
            </Group>
        );
    }


    getOldOrder(){
        return(
            <Group key="ListOrderOld"
                   title=" "
            >
                {this.getOrderType('old')}
            </Group>
        );
    }

    getCancelledOrder(){
        return(
            <Group key="ListOrderCancel"
                   title=" "
            >
                {this.getOrderType('cancel')}
            </Group>
        );
    }


    getOneTypeOrders(){
        var results = '';
        switch(this.props.typeOrderGet){
            case"now": results = this.getNowOrder();break;
            case"old": results = this.getOldOrder();break;
            case"cancel": results = this.getCancelledOrder();break;
        }

        return results;
    }

    getAllGroups(){
        return(
            this.getOneTypeOrders()
        );
    }


    render(){
        return(<div> {
                this.state.loadOrders
                    ?
                    <ScreenSpinner/>
                    :
                    this.getAllGroups()
            }
            </div>
        );
    }

}

export default OrderTypeList;