    setcountCartProduct(e,aa = false){
        if(e == 0){e = '';}
        if(aa === false) {
            this.setState({
                countCartProduct: e
            });
        }

        var sumCartItog = 0;
        this.state.cart.map((CPr)=>{
                sumCartItog += this.getSumCartOnDiscount(CPr);
            }
        );

        var SumDopsProduct = 0;
        if(Object.keys(this.state.DopProductUserSet).length > 0){

            Object.keys(this.state.DopProductUserSet).map((id,n)=>{
                    SumDopsProduct += this.state.DopProductUserSet[id].count*(this.state.DopProductUserSet[id].price*1)
            });
        }

        //console.log(SumDopsProduct);
        this.setState({sumCart:sumCartItog+SumDopsProduct});

        document.cookie = "cart = "+this.state.cart;
    }

    getSumCartOnDiscount(typeProduct){
        if(!("price_discount" in typeProduct) || typeProduct.price_discount === false){
            return (typeProduct.price*typeProduct.count);
        }else{
            return(typeProduct.price_discount.new_price*typeProduct.count);
        }
    }

