    getCartDB(id_user,countReloadBasket = 0){

        if(countReloadBasket>0){
           // console.log('   ,  ');
        }else {
            if (this.state.loading === false) {
                this.setState({loading: true});
                var ia = this.state.countReloadBasket+1;
                this.setState({countReloadBasket:ia});
               // console.log('  ',ia);
            }

        }

        axios({
            method:'get',
            url:'https://-./api/getBasketVK&apiKey='+global.ApiKey+'&updateBasket=1minute&id_user_vk='+id_user,
            responseType:'text'
        }).then(({ data }) => {
            //console.log("  ",data);
            if("error" in data){
                data.listProduct = [];
                console.log(data.error,'|',data.errorInfo);
            }

            if(data.listProduct === null){
                data.listProduct = [];
            }

            var ListOld = JSON.stringify(this.state.cart);
            var ListNew = JSON.stringify(data.listProduct);
            var ListPresetsOld = JSON.stringify(this.state.dopPresets);

            var ListPresetsNew = JSON.stringify(data.DopPresetsData);
            if(ListPresetsNew == 'false'){
                ListPresetsNew = '[]';
            }else{
                ListPresetsNew = JSON.stringify(data.DopPresetsData.listPresetProduct)
            }

            var statusBasket = this.StatusReloadBasketData(ListOld,ListNew);
            var statusPresets = this.StatusReloadBasketData(ListPresetsOld,ListPresetsNew);

            if(statusBasket === true) {
                this.ReloadBasketDataFN(data.listProduct);
                this.PlayInfoAudio('ReloadBasket');
            }

            if("DopProductList" in data) {
                var ListDopOld = JSON.stringify(this.state.DopProductList);
                var ListDopNew = JSON.stringify(data.DopProductList);

                var statusDopProduct = this.StatusReloadBasketData(ListDopOld,ListDopNew);

                if(statusDopProduct === true) {
                    this.setState({"DopProductList": data.DopProductList});
                    //console.log(ListDopOld,ListDopNew,'|||||',data.DopProductList);
                }else{
                   // console.log(ListDopOld,ListDopNew,'|||||',data.DopProductList);
                }
            }

            if(statusPresets === true || ListPresetsNew ===false) {
                this.ReloadBasketDataDopPresets(data.DopPresetsData);
                //console.log('  ',ListPresetsNew,' -|-|-|- ',ListPresetsOld);
            }



        })
            .catch(({err}) => {
                console.log("ERROR: ------  ",err);
            });

    }

