import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import {Alert, Div,Input,FormLayout, Button, Select, Search, Footer, Cell } from '@vkontakte/vkui';
import axios from "axios";
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';

class FormNewAddress extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
                city: '',
                street: '',
                streetSearch: '',
                home: '',
                entrance: '',
                level: '',
                flat: '',
                comment:'',
            streetCityVisual:'',
            streetName:null,
            streetList:{},
            search:'',
            pauseLoadStreet:true
        }

        this.addressItems = [
            // { label: '', name: 'city', statusChech:'yes', minValue:5, valPls:'' },
            //{ label: ' ', name: 'street', statusChech:'yes', minValue:4, valPls:'' },
            { label: ' ', name: 'home', statusChech:'yes', minValue:0, valPls:'' },
            { label: ' /', name: 'flat', statusChech:'', minValue:0, valPls:'' },
            { label: ' ', name: 'entrance', statusChech:'', minValue:0, valPls:'' },
            { label: '', name: 'level', statusChech:'', minValue:0, valPls:''},
            { label: '', name: 'comment', statusChech:'', minValue:0, valPls:':  ' }
        ];

        this.onChange = this.onChange.bind(this);
        this.onInput = this.onInput.bind(this);
        this.openDefault = this.openDefault.bind(this);

        this.closePopout = this.closePopout.bind(this);

        if(this.props.AddressEdit){
            this.state = {
                    id:this.props.AddressEdit.id,
                    city: this.props.AddressEdit.city,
                    street: this.props.AddressEdit.street,
                    home: this.props.AddressEdit.home,
                    entrance: this.props.AddressEdit.podezd,
                    level: this.props.AddressEdit.etaj,
                    flat: this.props.AddressEdit.kvartira,
                    comment: this.props.AddressEdit.comment,
                streetCityVisual:this.props.AddressEdit.city+' '+this.props.AddressEdit.street,
                streetName:this.props.AddressEdit.street,
                streetList:{},
                search:this.props.AddressEdit.street,
                }
        }
    }

    onChange(e) {
        const { name, value } = e.currentTarget;
        // if((value === null || value === '') & name === 'city'){}else {
        //     this.setState({[name]: value});
        // }
        this.setState({[name]: value});
       // console.log(this.state);
    }

    onInput(e) {
        console.log(e);
        this.setState({ search: e });
        if(e.length >2) {
            this.getStreetList(e, 1);
        }else{
            this.setStreetList({});
        }
        // console.log(e.target.value,'');
        // this.getStreetList(e.target.value,1)
        // return value;
    }


    openDefault () {
        this.props.setPopout({ popout:
                <Alert
                    actions={[{
                        title: '',
                        autoclose: true,
                        style: 'cancel'
                    }]}
                    onClose={this.closePopout}
                >
                    <h2>    </h2>
                    <p>   ,    !</p>
                </Alert>
        });
    }



    saveNewAddress(){
        if(this.state.city === '' || this.state.street === '' || this.state.home === ''){
            this.openDefault();
        }else{
           // console.log(' ,    ');
            this.props.setPopout({ popout:<ScreenSpinner />});
            //     .

            axios('https://-./api/setAddressUser&apiKey='+global.ApiKey+'',
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                    data: {
                        addressInfo:this.state,
                        idUserVK:this.props.userInfo.id
                    },
                    responseType:'text'
                })
                .then(({ data }) => {
                    if("error" in data){
                        console.log(data.error,'|',data.errorInfo);
                    }else{
                        if(data.result.result_add === true){
                            console.log(' ',data);
                                this.props.getAddressUserSystem();
                            this.props.setPopout({ popout:null});
                            this.props.setAddressDefault(0);
                        }else{
                            console.log(' ',data);
                        }
                    }
                })
                .catch((err) => {
                    console.log("ERROR: ---  ---  ",err);
                });
        }
    }

    saveEditAddress(){
        if(this.state.city === '' || this.state.street === '' || this.state.home === ''){
            this.openDefault();
        }else{
           // console.log(' ,    ');
            this.props.setPopout({ popout:<ScreenSpinner />});
            //     .

            axios('https://-./api/editAddressUser&apiKey='+global.ApiKey+'',
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                    data: {
                        addressInfo:this.state,
                        idUserVK:this.props.userInfo.id
                    },
                    responseType:'text'
                })
                .then(({ data }) => {
                    if("error" in data){
                        console.log(data.error,'|',data.errorInfo);
                    }else{
                        if(data.result.result_add === true){
                            console.log(' ',data);
                                this.props.getAddressUserSystem();
                            this.props.setPopout({ popout:null});
                            //this.props.setAddressDefault(0);
                        }else{
                            console.log(' ',data);
                        }
                    }
                })
                .catch((err) => {
                    console.log("ERROR: ---  ---  ",err);
                });
        }
    }

    getStreetList(S,idC){
        console.log(S,idC,'');
        this.setState({pauseLoadStreet:true});
       // this.props.setPopout({ popout:<ScreenSpinner />});
        axios('https://-./api/getStreetList&apiKey='+global.ApiKey+'',
            {
                method: 'GET',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                data: {
                    nameStreet:S,
                    city_users_id:idC
                },
                params:{
                    nameStreet:S,
                    city_users_id:idC
                },
                responseType:'text'
            })
            .then(({ data }) => {
                if("error" in data){
                    console.log(data.error,'|',data.errorInfo);
                }else{
                    if("result" in data){
                        console.log('  ',data.result);
                        this.setStreetList(data.result);
                       // this.props.setPopout({ popout:null});
                    }else{
                        console.log(' ',data);
                    }
                }
            })
            .catch((err) => {
                console.log("ERROR: ---  ---  ",err);
            });
    }


    closePopout () {
        this.props.setPopout({ popout: null });
    }

    getCitySelect(idCity,def = null){
        if(idCity in global.CityListBind) {
            const ListCityOrigin = global.CityListBind[idCity].listCity;
            const ListCitys = Object.keys(global.CityListBind[idCity].listCity);
            const ListOption = ListCitys.map((data, key) => {
                return (<option
                    key={key}
                    value={ListCityOrigin[data].title}
                >
                    {
                        ListCityOrigin[data].title
                    }
                </option>);
            });
            return (
                ListOption
            );
        }else{
            return ("");
        }
    }

    setStreetList(list){
        this.setState({streetList : list,pauseLoadStreet:true});
        console.log('',this.state.streetList);
        this.setState({pauseLoadStreet:false});
    }

    setStreetCityAdres(Localitytype,LocalityName,LocalityId,typeStreet,streetName) {
        this.setState({
            city: LocalityName,
            street: streetName,
            streetSearch: streetName,
            streetCityVisual: Localitytype+' '+LocalityName+' '+typeStreet + ' ' + streetName,
        });
    }



    getFormSetStreetName(){
        return(
            <div className="searckBoxses">
                <Search value={this.state.search}  onChange={this.onInput} after={null} placeholder=" "/>
                <div style={{minHeight:"400px"}}>
                {this.state.pauseLoadStreet === true ? "" :
                    Object.keys(this.state.streetList).length > 0 ?
                        Object.keys(this.state.streetList).map((number,id) =>
                            <Cell onClick={()=>{this.setStreetCityAdres(this.state.streetList[number].typeLocality,this.state.streetList[number].LocalityName,this.state.streetList[number].idLocality,this.state.streetList[number].typeStreet,this.state.streetList[number].name);}} className="streetCellChecker" key={this.state.streetList[number].id}>
                                <div className="nameCity">{this.state.streetList[number].LocalityName}</div>
                                <div className="nameStreet">{this.state.streetList[number].typeStreet} {this.state.streetList[number].name}</div>
                            </Cell>
                        )
                        :
                        <Cell className="streetCellChecker" key="noneStreet">
                              
                        </Cell>
                }
                </div>
                <div style={{padding:"160px 0"}}></div>
            </div>
        );
    }


    getOkStreetNameValue(){
        return(<FormLayout>
            {/*<Select placeholder=" /  " onChange={this.onChange} name="city" key={"input_city"} top="">*/}
            {/*    {this.getCitySelect(1)}*/}
            {/*</Select>*/}
            <Input
                type="text"
                name="street"
                key={"input_street"}

                value={
                    this.state.streetCityVisual
                }
                status='valid'
                onClick={()=>{this.setState({search:this.state.streetSearch,street:'',streetSearch:''})}}
            />
            {this.addressItems.map(({ label, name, statusChech, minValue, valPls }) => (
                <Input
                    type="text"
                    name={name}
                    key={"input_"+name}
                    top={label}
                    onChange={this.onChange}
                    value={
                        this.state[name]
                    }
                    status={
                        statusChech ?
                            this.state[name] ?
                                this.state[name].length >=minValue  ? 'valid' : 'error'
                                : 'error'
                            : ""
                    }
                    bottom={
                        statusChech ?
                            this.state[name] ?
                                this.state[name].length >=minValue  ? '' : '    '+this.state[name].length
                                : '  '
                            : ""
                    }
                    placeholder={valPls}
                />
            ))}
            <Button size="l" style={{background:"#ff7300"}} onClick={()=>{this.saveNewAddress(); if(this.props.hideModals){this.props.hideModals()}}}> </Button>
        </FormLayout>);
    }


    getFormSetStreetNameEdit(){
        return(
            <div className="searckBoxses">
                <Search value={this.state.search}  onChange={this.onInput} after={null}  placeholder=" "/>
                <div style={{minHeight:"400px"}}>
                {this.state.pauseLoadStreet === true ? "" :
                    Object.keys(this.state.streetList).length > 0 ?
                        Object.keys(this.state.streetList).map((number,id) =>
                            <Cell onClick={()=>{this.setStreetCityAdres(this.state.streetList[number].typeLocality,this.state.streetList[number].LocalityName,this.state.streetList[number].idLocality,this.state.streetList[number].typeStreet,this.state.streetList[number].name);}} className="streetCellChecker" key={this.state.streetList[number].id}>
                                <div className="nameCity">{this.state.streetList[number].LocalityName}</div>
                                <div className="nameStreet">{this.state.streetList[number].typeStreet} {this.state.streetList[number].name}</div>
                            </Cell>
                        )
                        :
                        <Cell className="streetCellChecker" key="noneStreet">
                              
                        </Cell>
                }
                </div>
                <div style={{padding:"160px 0"}}></div>
            </div>
        );
    }


    getOkStreetNameValueEdit(){
        return(<FormLayout>

            <Input
                type="text"
                name="street"
                key={"input_street"}

                value={
                    this.state.streetCityVisual
                }
                status='valid'
                onClick={()=>{this.setState({search:this.state.streetSearch,street:'',streetSearch:''})}}
            />

            {this.addressItems.map(({ label, name, statusChech, minValue, valPls }) => (
                <Input
                    type="text"
                    name={name}
                    key={"input_"+name}
                    top={label}
                    onChange={this.onChange}
                    status={
                        statusChech ?
                            this.state[name] ?
                                this.state[name].length >minValue  ? 'valid' : 'error'
                                : 'error'
                            : ""
                    }
                    value={this.state[name]}
                    bottom={
                        statusChech ?
                            this.state[name] ?
                                this.state[name].length >minValue  ? '' : '   '
                                : '  '
                            : ""
                    }
                    placeholder={valPls}
                />
            ))}
            <Button size="l" style={{background:"#ff7300"}} onClick={()=>{this.saveEditAddress(); if(this.props.hideModals){this.props.hideModals()}}}> </Button>
        </FormLayout>);
    }


    getNulledForm(){
        return(
                this.state.street !== '' ? this.getOkStreetNameValue() : this.getFormSetStreetName()
        );
    }


    getEditForm(){
        return(
            this.state.street !== '' ? this.getOkStreetNameValueEdit() : this.getFormSetStreetNameEdit()
        );
    }

    render(){
        return(
            <Div>
                {this.props.AddressEdit ? this.getEditForm() : this.getNulledForm()}
            </Div>
        );
    };
}

export default FormNewAddress;