import React, { useState, useEffect } from 'react';
import {Div, View, Panel, Separator, PanelHeader, Group, Button, CellButton, HeaderButton, platform, IOS, Tooltip, HorizontalScroll, Placeholder, Cell } from '@vkontakte/vkui';

import Icon56MarketOutline from '@vkontakte/icons/dist/56/market_outline';

import Icon56NotificationOutline from '@vkontakte/icons/dist/56/notification_outline';
import Icon24Notification from '@vkontakte/icons/dist/24/notification';
import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';

const osName = platform();


class Notice extends React.Component {

    constructor (props)
    {
        super(props);
        this.state = {
            activePanel:"home",
            popout:null
            //popout:"<ScreenSpinner size='large' />",
        }
    }

    ActivePanels(e){
        this.setState({activePanel: e});
    }

    NoteNotice(){
        return(
            <Placeholder stretched
                         icon={<Icon56NotificationOutline />}
                         action={<Button size="l" level="tertiary" onClick={()=>{this.props.setState({activeStory: 'catalog'});}}> </Button>}

            >
                <h2> </h2>
                <p>     ,   !</p>
            </Placeholder>
        );
    }

    ReverceList(Pr){
        var res = Object.keys(Pr).reduceRight(function (arr, last, index, coll) {
            // console.log(last, index);
            return (arr = arr.concat(last))
        }, []);
        return res;
    }


    setNoticeData(type,idNotice,statusView){
        if(type == 1) {
            this.props.setNotice(type, idNotice);
            this.props.DeleteNotifi(idNotice);
        }else{
            if(statusView === false){
                this.props.setNotice(type, idNotice);
            }
        }
    }

    getImagesNotice(img){
        if(img === null || img == ''){return ("");}else{
            return(
                <img src={img} style={{width:"100%"}}/>
            );
        }
    }

    getNotice(){

        const NoticeList = this.ReverceList(this.props.AllNotice).map((Data,id)=>{

            if(this.props.AllNotice[Data]['notice_view'] == '0'){
                var color = '#ff7300';
                var statusView = false;
            }else{
                var color = '';
                var statusView = true;
            }

            return(<Group
                key={"Notice_C_"+Data}
            >
                <Cell
                    before={<Icon24Notification
                        style={{color:color}}
                        onClick={() => {this.setNoticeData(0,this.props.AllNotice[Data]['id_notification'],statusView)}}
                    />}
                    asideContent={<Icon24Dismiss
                        onClick={() => {this.setNoticeData(1,this.props.AllNotice[Data]['id_notification'],statusView)}}
                    />}

                >
                    {this.props.AllNotice[Data]['title']}
                </Cell>
                {this.getImagesNotice(this.props.AllNotice[Data]['img'])}
                    <Div key={"Notice_"+Data} dangerouslySetInnerHTML={{__html: this.props.AllNotice[Data]['text'].replace(/\n/g, "<br />")}}>
                    </Div>
            </Group>);
        });

        return(
            NoticeList
        );
    }

    getNotificationPage(){
        if(this.props.StatusNotification === true){
            if(Object.keys(this.props.AllNotice).length<=0){
                return(this.NoteNotice());
            }else{
                return(this.getNotice());
            }
        }else{
            return(
                <Placeholder stretched
                             icon={<Icon56NotificationOutline />}
                             action={<Button size="l" level="tertiary"
                                             onClick={()=>{this.props.PushClick()}}
                             > </Button>}
                             style={{textAlign:"center"}}
                >
                      ,   
                </Placeholder>
            );
        }
    }


    render(){
        return(
            this.getNotificationPage()
        );

    };

}

export default Notice;