import React from 'react';

import connect from '@vkontakte/vk-connect';
import axios from "axios";

import {Group, Cell, Div, Counter, Button} from '@vkontakte/vkui';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Icon24Recent from '@vkontakte/icons/dist/24/recent';

import Icon24Dismiss from '@vkontakte/icons/dist/24/dismiss';
import Icon24DoneOutline from '@vkontakte/icons/dist/24/done_outline';
import Icon24Error from '@vkontakte/icons/dist/24/error';
import Icon24MoneyTransfer from '@vkontakte/icons/dist/24/money_transfer';
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';
import Icon24User from '@vkontakte/icons/dist/24/user';

class OrderShowDetail extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
    }


    componentDidMount() {
        //console.log('  : ',this.props.OrderDatas);
    }

    getStatusOrderText(a){
        var ststusReturn = "";

        switch(a){
            case"0": ststusReturn = ""; break;
            case"1": ststusReturn = ""; break;
            case"3": ststusReturn = ""; break;
            case"6": ststusReturn = ""; break;
        }
        return ststusReturn;
    }

    getStatusOrderReal(statusOld,statusReal){
        if(statusReal === null){
           // console.log(' ');
            return statusOld;
        }else{
            return statusReal.status;
        }
    }

    getStatusIconText(a){
        var iconsD = '';

        var ststusReturns = <Icon24Error />;

        switch(a){
            case"0": iconsD = <Icon24Recent /> ;break;
            case"1": iconsD = <Icon24DoneOutline /> ;break;
            case"3": iconsD = <Icon24Dismiss /> ;break;
            case"6": iconsD = <Icon24Recent /> ;break;
        }
        return ststusReturns;
    }


    getInfoPreorderBlock(Order){
        if(Order.pre_order.status === 'none'){
            return "";
        }else{
            return (
                <Group title="    ">
                    <Cell
                        before={<Icon24Recent />}
                    >
                        {Order.pre_order.date_pre_order}
                    </Cell>
                </Group>
            );
        }
    }

    escapeHtml(text,type) {
        switch(type){
            case"decode":
                var map = {
                    '&amp;': '&',
                    '&lt;': '<',
                    '&gt;': '>',
                    '&quot;': '"',
                    "&#039;": "'"
                };
                break;
            case"encode":
                var map = {
                    '&': '&amp;',
                    '<': '&lt;',
                    '>': '&gt;',
                    '"': '&quot;',
                    "'": '&#039;'
                };
                break;
        }

        return text.replace(/[&<>"']/g, function(m) { return map[m]; });
    }

    escapeHtmlDecode(text) {
        return text
            .replace(/&amp;/g, "&")
            .replace(/&lt;/g, "<")
            .replace(/&gt;/g, ">")
            .replace(/&quot;/g, "\"")
            .replace(/&#039;/g, "'");
    }


    getOrderComposition(Order){

        if(Order == '' || Order === null){
            return (<Group title=" ">
                <Div> </Div></Group>);
        }

        const ListProduct = Order.map((data,key)=>{

            if(data.sale_price != data.product_price){
                var Price_sales = <span><s>{data.product_price} </s> <b>{data.sale_price} </b> </span>;
            }else{
                var Price_sales = <span><b>{data.sale_price} </b> </span>;
            }

            if(data.discount_amount != '0'){
                var Discount = <b>: {data.discount_amount} </b>
            }else{
                var Discount = '';
            }

            return (
                <Cell key={"Product_cell_"+key}
                    description={<span key={"prices_product_"+key}>{Price_sales} - {data.count} . <i>{Discount}</i></span>}
                    indicator={<Counter>&nbsp;{data.cost} &nbsp;</Counter>}
                >
                    <span key={"product_title_"+key}>{this.escapeHtmlDecode(data.product_title)}</span>
                </Cell>
            );
        });
        return (<Group title=" ">
            {ListProduct}</Group>);
    }


    getOrderDopComposition(Order){

        if(Order == '' || Order === null){
            return ("");
        }

        const ListProduct = Order.map((data,key)=>{

            var Price_sales = <span><b>{data.price_product} </b> </span>;


            return (
                <Cell key={"Dop_Product_cell_"+key}
                    description={<span key={"prices_dop_product_"+key}>{Price_sales} - {data.count} .</span>}
                    indicator={<Counter>&nbsp;{data.cost} &nbsp;</Counter>}
                >
                    <span key={"dop_product_title_"+key}>{this.escapeHtmlDecode(data.title_product)}</span>
                </Cell>
            );
        });

        if(ListProduct == '' || ListProduct=== null){
            return ("");
        }

        return (<Group title="  ">
            {ListProduct}
        </Group>);
    }


    getOrderGiftComposition(Order){

        if(Order == '' || Order === null){
            return ("");
        }

        const ListProduct = Order.map((data,key)=>{

            var Price_sales = <span><b>{data.price_product} </b> </span>;


            return (
                <Cell key={"Gift_Product_cell_"+key}
                    description={<span key={"prices_Gift_product_"+key}>{Price_sales} - {data.count} .</span>}
                    indicator={<Counter>&nbsp;{data.cost} &nbsp;</Counter>}
                >
                    <span key={"Gift_product_title_"+key}>{this.escapeHtmlDecode(data.title_gift_product)}</span>
                </Cell>
            );
        });

        if(ListProduct == '' || ListProduct=== null){
            return ("");
        }

        return (<Group title="">
            {ListProduct}
        </Group>);
    }


    getOrderDeliveryInfo(Order){

        if(Order.dilivery.dilivery_type == ''){
            var Address = Order.dilivery.dilivery_adres;
            var PriceDeli = Order.dilivery.dilivery_price;
            var TextDel = '';
        }else{
            var Address = Order.affiliate.affiliate_info;
            var PriceDeli = " 0 ";
            var TextDel = '';
        }

            return (<Group title=" ">
                <Cell key={"Deliv_cell"}
                    description={Address}
                    indicator={<Counter>&nbsp; {PriceDeli}  &nbsp;</Counter>}
                >
                    {TextDel}
                </Cell>
                </Group>
            )
    }

    getInfoPromokod(Order){
        if(Order.promo_status == 'active'){
            var PromoInfo = <Group title="">
                <Div><b>{Order.promo_code}</b> {Order.promo_info.title}</Div>
            </Group>
        }else{
            var PromoInfo = '';
        }

        return PromoInfo;
    }

    getInfoPointsOrder(Order){
            var Points = <div>
                <Cell before={<Icon24Coins/>} > : {Order.point_out}</Cell>
                <Cell  before={<Icon24Coins/>} > : {Order.point_in}</Cell>
            </div>;

        return Points;
    }

    getComment(Order) {

        if(Order == '' || Order === null){
            return ("");
            Order = '';
        }

        return (
            <Group title="">
                <Div>{Order}</Div>
            </Group>
        );
    }

    getRepeatOrder(OrderId){
        //console.log(OrderId);
        return (
            <Group style={{textAlign:"center",padding:"10px 0"}}>
                <Button onClick={()=>{
                    this.setRepeatOrder(OrderId.id_order)
                }}></Button>
            </Group>
        );
    }

    setRepeatOrder(OrderId){
        //console.log(' ');
        axios({
            method:'get',
            url:'https://-./api/setRepeatOrder&apiKey='+global.ApiKey+'&idUserVK='+this.props.userData.id+'&OrderIdRepeat='+OrderId,
            responseType:'text'
        })
            .then(({data})=>{
                if("error" in data){
                    console.log(data);
                    alert(data.errorInfo);
                }else{
                   console.log('  : ',data);
                }
            })
            .catch((err) =>{
                console.log('  ',err);
            });
    }

    getPerson(Order) {

        if(Order == '' || Order === null){
            Order = '1';
        }

        // return (
        //     <Group title="- ">
        //         <Div>{Order}</Div>
        //     </Group>
        // );

        return (
            <Cell
                before={<Icon24User />}
            >
                : {Order}
            </Cell>
        );
    }


    render(){
        const Order = this.props.OrderDatas;
        const StatusIcon = this.getStatusIconText(Order.status_order);
        const detailText = <span> </span>;

        return(
            <div key="InfoOrderBlock" >
                <Group
                    title={detailText}
                >
                    <Cell
                        before={StatusIcon}
                    >
                        {this.getStatusOrderReal(this.getStatusOrderText(Order.status_order),Order.status_order_real)}
                    </Cell>
                    <Cell
                        before={<Icon24Recent />}
                    >
                        {Order.date} {Order.time}
                    </Cell>
                    <Cell
                        before={<Icon24MoneyTransfer />}
                    >
                        {Order.payment.type_pay}
                    </Cell>
                    <Cell
                        before={<Icon24MoneyCircle />}
                    >
                        {Order.sum_order} 
                    </Cell>
                    {this.getPerson(Order.person)}
                    {this.getInfoPointsOrder(Order)}
                </Group>
                    {this.getInfoPreorderBlock(Order)}
                {this.getInfoPromokod(Order.promo)}
                {this.getOrderComposition(Order.productList)}
                {this.getOrderDopComposition(Order.dopProductList)}
                {this.getOrderGiftComposition(Order.giftProductList)}
                {this.getOrderDeliveryInfo(Order)}
                {this.getComment(Order.Comment)}
                {this.getRepeatOrder(Order)}
            </div>
        );
    }

}

export default OrderShowDetail;