import React from 'react';

import connect from '@vkontakte/vk-connect';

import Group from '@vkontakte/vkui/dist/components/Group/Group';
import {List,Cell, Switch} from '@vkontakte/vkui/';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Icon24Notification from '@vkontakte/icons/dist/24/notification';
import Icon24Note from '@vkontakte/icons/dist/24/note';

import Icon24Coins from '@vkontakte/icons/dist/24/coins';
import Icon24CommentOutline from '@vkontakte/icons/dist/24/comment_outline';


class UserInfo extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            StatusNotification:false
        }
    }

    componentDidMount() {
        this.getStatusNotificate();
    }

    getPanelProfil(){
        return(
            <div id={this.props.id} style={{maxWidth:'1200px',margin:'0 auto'}} them='white'>
                {this.props.fetchedUser &&
                    <Group title=" 
    ">
                        <Cell
                            before={this.props.fetchedUser.photo_200 ? <Avatar src={this.props.fetchedUser.photo_200}/> : null}
                            description={this.props.fetchedUser.city && this.props.fetchedUser.city.title ? this.props.fetchedUser.city.title : ''}
                        >
                            {`${this.props.fetchedUser.first_name} ${this.props.fetchedUser.last_name}`}
                        </Cell>
                    </Group>
                }

                <Group>
                    <Cell
                        before={<Icon24Note />}
                        description="    "
                        onClick={this.props.setShowUserOrderList.bind()}
                    >
                         
                    </Cell>
                </Group>


                {this.props.dataUsers.dataUsers.error ?
                    this.getInfoUserNoneAuth()
                    :

                    <Group title="">
                        <List>
                            <Cell
                                before={<Icon24Coins />}
                            >
                                 : {this.props.dataUsers.dataUsers.dataUser.point}
                            </Cell>
                        </List>
                    </Group>
                }

                <Group>
                        <Cell
                            before={<Icon24Notification />}
                            asideContent={<Switch
                                checked={this.state.StatusNotification}
                                onChange={()=>{this.PushClick()}} />}
                            description="  , ..."
                        >
                            PUSH - 
                        </Cell>

                </Group>

                <Group>
                        <Cell
                            before={<Icon24CommentOutline />}
                        >
                            <a style={{color:"#252622",textDecoration:"none"}}
                               href={global.linkSocSeti} target="_blank">
                                  
                            </a>
                        </Cell>

                </Group>

            </div>
        );
    }

    getInfoUserNoneAuth() {
        return (
            <Div>
                   ,   !
            </Div>
        );
    }

    getStatusNotificate(){
        connect.sendPromise("VKWebAppCallAPIMethod", {"method": "apps.isNotificationsAllowed", "params": {"user_id": "328965469","v":"5.103","access_token":"a07170c1a07170c1a07170c1c4a01c7753aa071a07170c1fdf9a8b9585d7876a020ecbf"}})
            .then((datas)=> {
                this.setState({StatusNotification: datas.response.is_allowed});
                this.props.setStatusNotification(datas.response.is_allowed);
            })
            .catch((err)=>{
                this.setState({StatusNotification: false});
                this.props.setStatusNotification(false);
            });
    }

    //defaultChecked

    PushClick(){
        if(this.state.StatusNotification === true ){
            this.OffPushNotyfication();
        }else{
            this.OnPushNotyfication();
        }
    }

    OffPushNotyfication(){
        connect.sendPromise("VKWebAppDenyNotifications", {})
            .then((datas)=>{
                this.setState({StatusNotification: false});
                this.props.setStatusNotification(false);
            })
            .catch((err)=> {
                this.setState({StatusNotification: false});
                this.props.setStatusNotification(false);
                }
            );
    }

    OnPushNotyfication(){
        connect.sendPromise("VKWebAppAllowNotifications", {})
            .then((datas)=>{
                this.getStatusNotificate();
            })
            .catch((err)=> {
                    this.getStatusNotificate();
                }
            );
    }

    render(){
        return(
            this.getPanelProfil()
        );
    }
}
export default UserInfo;
