import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import {Div,Input,FormLayout,FormLayoutGroup,Button,Snackbar,Avatar, View, Panel, PanelHeader, Group, CellButton, HeaderButton, platform, IOS, Tooltip } from '@vkontakte/vkui';
import axios from "axios";
import GetOneAffiliateAddress from "./GetOneAffiliateAddress";
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';

class AffiliateList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            listAffiliate:[],
            addressAffiliateOk:false,
            dataAffiliate:""
        }
    }

    componentDidMount() {
        axios('https://-./api/getAffiliate&apiKey='+global.ApiKey+'',
            {
                method: 'POST',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                data: {
                    id_City: 1,
                    id_UserVK:this.props.userInfo.id
                },
                responseType:'text'
            })
            .then(({ data }) => {
                if("error" in data){
                    console.log(data.error,'|',data.errorInfo,'');
                }else{
                    //console.log(data,'');
                    if(Object.keys(data.listOffAffiliate).length>0){
                       // console.log('  ,  ');
                        Object.keys(data.listOffAffiliate).map((datas,keys)=>
                            {
                                if(this.props.AffiliateDefault === keys){
                                    //console.log('      ,   ', keys);
                                    this.props.setAffiliateDefault(0);
                                }else{
                                   // console.log('      ,    ',keys);
                                }
                            }
                        );
                        this.setState({listAffiliate:data.list});
                        this.getAddressAffiliate(this.state.listAffiliate);
                        this.props.setStates({listAffiliateGlobal:data.list});
                    }else{
                        this.setState({listAffiliate:data.list});
                        this.getAddressAffiliate(this.state.listAffiliate);
                        this.props.setStates({listAffiliateGlobal:data.list});
                    }

                }
            })
            .catch((err) => {
                console.log("ERROR: --- ---  ",err);
            });
    }

    setAddressInfoUsers(e,type=1){
        if(type===1){
            var listAddressU = Object.keys(this.state.listAffiliate);
            // console.log(listAddressU,"  ");

            // console.log('    ',this.state.listAddress[listAddressU[0]].diliveryData.priceDilivery);

            var DataSetAddress = this.state.listAffiliate[listAddressU[e]];
            this.props.setStates({
                addressOrderInfoUser: DataSetAddress.title
            });
        }
    }


    getAddressAffiliate(listAddress) {
        this.props.getDeliveryDataPriceItogAffiliate();
        this.setAddressInfoUsers(this.props.AffiliateDefault);
        this.state.dataAffiliate = Object.keys(listAddress).map((dataAffiliate, keyId) =>
            <GetOneAffiliateAddress
                key={'Affiliate_'+keyId}
                AddresInfo={listAddress[dataAffiliate]}
                NumberAddress={keyId}
                AffiliateDefault={this.props.AffiliateDefault}
                setAffiliateDefault={this.props.setAffiliateDefault.bind(this)}
                setAddressInfoUsers={this.setAddressInfoUsers.bind(this)}
            />
        );
       this.setState({addressAffiliateOk:true});
        //this.state.addressOk:true;
        //return ArrayPr;
    }



    render() {
        return (
            this.state.addressAffiliateOk === false ? <div><h3> </h3> <ScreenSpinner/> <p></p></div> : this.state.dataAffiliate
        );
    };
}

export default AffiliateList;