import React, { useState, useEffect } from 'react';
import axios from "axios";
import {Div,Radio, Input,FormLayout,Group,Button, List, Cell, Snackbar,Avatar,Textarea, View, Panel, PanelHeader, CellButton, HeaderButton, platform, IOS, Tooltip, Slider, Select, Placeholder } from '@vkontakte/vkui';
import ym from 'react-yandex-metrika';

class NewOrderServer extends React.Component{

    constructor (props)
    {
        super(props);
        this.state = {
        };

    }

    setDataServer(){
        this.props.setStates({statusNewOrder:' '});
        axios('https://-./api/setNewOrder&apiKey='+global.ApiKey+'',
            {
                method: 'POST',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                data: {
                    idUserVK: this.props.userInfo.id,
                    dataOrder:this.props.DataOrderInfo
                },
                responseType:'text'
            })
            .then(({ data }) => {
                if("error" in data){
                    console.log(data.error,'|',data.errorInfo);
                }else{
                   // console.log('  ',data);
                    if(data.status == "OK"){
                        this.props.getPageOkOrderSet({newOrderStats:true});
                        this.props.setOkNewOrderView();
                        try{
                            if(global.Metrika.Markers[4].active == '1') {
                                ym('reachGoal', global.Metrika.Markers[4].marker);
                            }
                        }catch (e) {

                        }
                    }
                }
            })
            .catch((err) => {
                console.log("ERROR: --- ---  ",err);
            });
    }

    render() {
        return (
            <Button size="xl" onClick={()=>{this.setDataServer()}} className="WeightFontBold"> </Button>
        );
    };
}

export default NewOrderServer;