import React from 'react';
import connect from '@vkontakte/vk-connect';
import {Div, Group, List,Cell, Switch, Select, FormLayout} from '@vkontakte/vkui/';
import axios from "axios";


class Preorder extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loadBlockPreorder:false,
            listDatePreorder:[],
            SelectDatePreorder:null,
            SelectTimePreorder:null
        }

        this.onChangeS = this.onChangeS.bind(this);
    }

    componentDidMount() {
        this.setState({loadBlockPreorder:true});
        this.getDateList();
        this.setState({
            SelectDatePreorder:this.props.DataInfos.SelectDatePreorder,
            SelectTimePreorder:this.props.DataInfos.SelectTimePreorder
        });
        console.log(this.props.DataInfos.SelectDatePreorder);
    }

    getDateList(){
        axios('https://-./api/getPreorderDate&apiKey='+global.ApiKey+'',
            {
                method: 'GET',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                data: {
                },
                responseType:'text'
            })
            .then(({ data }) => {
                if("error" in data){
                    console.log(data.error,'|',data.errorInfo);
                }else{
                    this.setState({listDatePreorder:data.list});
                    console.log(data.list);
                }
            })
            .catch(({err}) => {
                console.log("ERROR: --- ---  ",err);
            });
    }

    onChangeS(e) {
        const { name, value } = e.currentTarget;
        // if((value === null || value === '') & name === 'city'){}else {
        //     this.setState({[name]: value});
        // }
        this.setState({[name]: value});
        this.props.setStates({[name]: value});
        if(name === 'SelectDatePreorder'){
            this.setState({SelectTimePreorder: null});
            this.props.setStates({SelectTimePreorder: null});
        }
        //console.log(this.state);
    }

    getSelectDate(){
        const ListDate = Object.keys(this.state.listDatePreorder).map((data,n)=>{
            return(
                <option key={data+"preorderDates"} value={data}>{this.state.listDatePreorder[data].title}</option>
            );
        });
        return (
            <div>
            <Cell>
                <Select
                    top="  "
                    key="datePreorder"
                    placeholder=" "
                    name="SelectDatePreorder"
                    onChange={this.onChangeS}
                    defaultValue={this.props.DataInfos.SelectDatePreorder}
            >{ListDate}</Select>
            </Cell>
            <Cell>
                {this.getSelectTime()}
            </Cell>
            </div>
        );
    }

    getSelectTime(){
        if(this.state.SelectDatePreorder in this.state.listDatePreorder) {
            var ListTimes = this.state.listDatePreorder[this.state.SelectDatePreorder].timeList;
            const ListTime = Object.keys(ListTimes).map((times, n) => {
                if(ListTimes[times].false === true){
                    return ('');
                }else {
                    return (
                        <option key={times + "preorderTimes"}
                                value={times}
                        >
                            {ListTimes[times].title}
                        </option>
                    );
                }
            });
            return (
                    <Select
                    top="  "
                    key="datePreorderTime"
                    placeholder=" "
                    name="SelectTimePreorder"
                    onChange={this.onChangeS}
                    defaultValue={this.props.DataInfos.SelectTimePreorder}
                >{ListTime}</Select>
            );
        }else{
            return ('');
        }
    }

    getBlockPreorder(){
        return(
                <Div key="DivPreorder">
                    {
                        this.state.loadBlockPreorder ?
                            this.getSelectDate()
                            :
                            "   "
                    }
                </Div>
        );
    }

    render(){
        return(
            this.getBlockPreorder()
        );
    }
}
export default Preorder;
