import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import {Alert,Div,Radio,Input,FormLayout,FormLayoutGroup,Button,Snackbar,Avatar, View, Panel, PanelHeader, Group, CellButton, HeaderButton, platform, IOS, Tooltip } from '@vkontakte/vkui';
import Icon28EditOutline from '@vkontakte/icons/dist/28/edit_outline';
import Icon28CancelCircleOutline from '@vkontakte/icons/dist/28/cancel_circle_outline';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import axios from "axios";

class GetOneAddress extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            AddressN:''
        }

        this.closePopout = this.closePopout.bind(this);
    }

    getAddressTitle(){
        var title = '';

        if(this.props.AddresInfo.comment !== ''){
            title = this.props.AddresInfo.comment;
        }else{
            title = <span>{this.props.AddresInfo.street} {this.props.AddresInfo.home}</span>;
        }

        return(
            <span>
                {title}
            </span>
        );
    }

    deletedAddress(a){
            this.props.setPopout({ popout:<ScreenSpinner />});

            axios('https://-./api/deletedAddressUser&apiKey='+global.ApiKey+'',
                {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                    data: {
                        addressInfo:{id:a},
                        idUserVK:this.props.userInfo.id
                    },
                    responseType:'text'
                })
                .then(({ data }) => {
                    if("error" in data){
                        console.log(data.error,'|',data.errorInfo);
                    }else{
                        if(data.result.result_add === true){
                            this.props.setAddressDefault('del');
                           // console.log(' ',data);
                            this.props.getAddressUserSystem(true);
                            this.props.setPopout({ popout:null});
                        }else{
                            console.log(' ',data);
                        }
                    }
                })
                .catch((err) => {
                    console.log("ERROR: ---  ---  ",err);
                });
    }

    closePopout () {
        this.props.setPopout({ popout: null });
    }


    openDefault (a) {
        this.props.setPopout({ popout:
                <Alert
                    actions={[{
                        title: '',
                        autoclose: true,
                        style: 'cancel'
                    }, {
                        title: '',
                        autoclose: true,
                        action: () => this.deletedAddress(a),
                    }]}
                    onClose={this.closePopout}
                >
                    <h2> </h2>
                    <p>     ?</p>
                </Alert>
        });
    }


    getAddressFull(n){
        return(<div>
                <span style={{width:"75%",float:"left"}}>
                 . {this.props.AddresInfo.city}, . {this.props.AddresInfo.street}, . {this.props.AddresInfo.home},  {this.props.AddresInfo.podezd},  {this.props.AddresInfo.etaj}, . {this.props.AddresInfo.kvartira}
                </span>
                <span style={{width:"24%",float:"right"}}><Icon28EditOutline
                    onClick={
                        ()=>{
                            // console.log('  ',n);
                            this.props.setAddressEdit(this.props.AddresInfo.id);
                        }
                    }
                    style={{float:"left",marginRight:"10px"}}/> <Icon28CancelCircleOutline style={{float:"left"}}
                                                                                           onClick={
                                                                                               ()=>{
                                                                                                   // console.log('  ');
                                                                                                   this.openDefault(this.props.AddresInfo.id);
                                                                                               }
                                                                                           }
                /></span>
            </div>
        );
    }

    defaultCheckedR(e,n){
        if(e === n){
            return (
                <Radio name="DiliveryAddress" value={this.props.AddresInfo.id} description={this.getAddressFull(n)} defaultChecked onClick={() => {this.props.setAddressDefault(n);this.props.setDataDeliveryPrice(n,' ');this.props.setDeliveryDataItogs();this.props.setAddressInfoUsers(n);}}>{this.getAddressTitle()}</Radio>
            );
        }else{
            return(
                <Radio name="DiliveryAddress" value={this.props.AddresInfo.id} description={this.getAddressFull(n)} onClick={() => {this.props.setAddressDefault(n);this.props.setDataDeliveryPrice(n, ' ');this.props.setDeliveryDataItogs();this.props.setAddressInfoUsers(n);}}>{this.getAddressTitle()}</Radio>
            );
        }
    }

    render(){

        return(
            this.defaultCheckedR(this.props.addressDefault,this.props.NumberAddress)
        );
    };
}

export default GetOneAddress;