import React from 'react';
import { Div } from '@vkontakte/vkui';
//import {Initializer as YM} from 'react-yandex-metrika';
import ym from 'react-yandex-metrika';


function escapeHtmlDecode(text) {
    return text
        .replace(/&amp;/g, "&")
        .replace(/&lt;/g, "<")
        .replace(/&gt;/g, ">")
        .replace(/&quot;/g, "\"")
        .replace(/&#039;/g, "'");
}

export default function OneProduct({product,idCategory, ClickProduct, setCart, setProductModal, getButtonInCartProduct}) {
// console.log('    ',ClickProduct);
    // const ArrayProducts = product.map(oneProduct =>
    //     //<OneProduct key={oneProduct.id} product={oneProduct} />
    //     <Div className="blockProduct" key={"id_product_"+oneProduct.id_Product}>
    //         <Div className="imgPrewieProduct"><img alt={oneProduct.title} src={oneProduct.img}/></Div><Div className="titleProduct">{oneProduct.title}</Div><Div className="DescrProduct">{oneProduct.description}</Div><Div className="priceProductBig"> <span className="priceProduct">{oneProduct.price} .</span></Div>
    //     </Div>
    // );

    // console.log(key);

    function escapeHtml(text) {
        var map = {
            '&': '&amp;',
            '<': '&lt;',
            '>': '&gt;',
            '"': '&quot;',
            "'": '&#039;'
        };

        return text.replace(/[&<>"']/g, function(m) { return map[m]; });
    }


    function number_format(number, decimals, dec_point, separator ) {
        number = (number + '').replace(/[^0-9+\-Ee.]/g, '');
        var n = !isFinite(+number) ? 0 : +number,
            prec = !isFinite(+decimals) ? 0 : Math.abs(decimals),
            sep = (typeof separator === 'undefined') ? ',' : separator ,
            dec = (typeof dec_point === 'undefined') ? '.' : dec_point,
            s = '',
            toFixedFix = function(n, prec) {
                var k = Math.pow(10, prec);
                return '' + (Math.round(n * k) / k)
                    .toFixed(prec);
            };
        //    IE parseFloat(0.55).toFixed(0) = 0;
        s = (prec ? toFixedFix(n, prec) : '' + Math.round(n))
            .split('.');
        if (s[0].length > 3) {
            s[0] = s[0].replace(/\B(?=(?:\d{3})+(?!\d))/g, sep);
        }
        if ((s[1] || '')
            .length < prec) {
            s[1] = s[1] || '';
            s[1] += new Array(prec - s[1].length + 1)
                .join('0');
        }
        return s.join(dec);
    }

    function getWeightProduct(Product_Weight,Product_WeightType){
        if(Product_WeightType != ''){
            return(
                number_format(Product_Weight,'0','.', ' ')+' '+Product_WeightType
            );
        }else{
            return(
                Product_Weight+' '+Product_WeightType
            );
        }
    }

    function getModyficateBlock(ArrayMod){
        var ListMod = Object.keys(ArrayMod);
        if(ListMod.length <=0){
            return "";
        }else{
            var VisualList = ListMod.map((datas,keys)=>{
                return (<div key={keys} className="ModyficateProduct"><img src={ArrayMod[datas].mod_img} /></div>);
            });
            return(
                <div className="ModyficateBlock">{VisualList}</div>
            );
        }
    }

    function getProductDiv(productList,idCategory,ClickProduct,setCart,setProductModal ,getButtonInCartProduct){

         //console.log(ClickProduct,'  ');
        var ProductCat = productList;

        const WeightProduct = '';



        const ArrayProducts = ProductCat.map((typeProduct,idTypeProduct) =>
            <Div
                key={"blockProducts_"+typeProduct.id_Product+"_"+idTypeProduct}
                className="blockProduct"
                id={"id_product_"+typeProduct.id_Product+"_type_"+typeProduct.type_product}
            >
                {getModyficateBlock(typeProduct.modyficate)}
                <Div className="imgPrewieProduct">
                    <img
                        alt={typeProduct.title}
                        src={typeProduct.img}
                        onClick={() => {
                            ClickProduct('Panels_Product');
                            setProductModal([typeProduct]);
                            console.log('  ', ProductCat, idTypeProduct);
                            try {
                                if (global.Metrika.Markers[3].active == '1') {
                                    ym('reachGoal', global.Metrika.Markers[3].marker);
                                }
                            }catch (e) {
                                
                            }
                            //console.log('reachGoal',global.Metrika.Markers[3].marker);
                            // {ym(global.Metrika.idCounter, 'reachGoal', global.Metrika.Markers[3].marker)}
                        }
                        }
                    />
                </Div>
                <Div className="titleProduct">
                    {/*{escapeHtmlDecode(typeProduct.title)}*/}
                    <span className="titleProductOne">{escapeHtmlDecode(typeProduct.title)}</span>
                    <span className="redTextP">
                        {getWeightProduct(typeProduct.weight,typeProduct.weight_type)}
                    </span>
                    <div className="clr"></div>
                </Div>
                <Div className="DescrProduct">
                    {escapeHtmlDecode(typeProduct.description)}
                    <div>
                    </div>
                    {typeProduct.type_info != '' ?
                        <div className="WeightBlock">
                            {typeProduct.type_info}
                        </div>
                        :
                        ""
                    }
                </Div>
                <Div className="priceProductBig">

                        {getPriceProducts(typeProduct)}

                    {getButtonInCartProduct(typeProduct,setCart)}
                </Div>
            </Div>
        );


        return ArrayProducts;
    }

    function getPriceProducts(typeProduct){
        if(!("price_discount" in typeProduct) || typeProduct.price_discount === false){
            return (<span className="priceProduct">
                {number_format(typeProduct.price,'0','.',' ')} 
                </span>
            );
        }else{
            return(
                <span className="priceProduct">
                    <s className="old_price_style" style={{fontWeight:"460",fontSize:"0.9em",marginRight:"5px"}}>{number_format(typeProduct.price_discount.old_price,'0','.',' ')} </s>
                    {number_format(typeProduct.price_discount.new_price,'0','.',' ')} 
                </span>
            );
        }
    }



    return(
        getProductDiv(product,idCategory,ClickProduct, setCart , setProductModal, getButtonInCartProduct)
    );
}