import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import {Div,Radio,Input,FormLayout,FormLayoutGroup,Button,Snackbar,Avatar, View, Panel, PanelHeader, Group, CellButton, HeaderButton, platform, IOS, Tooltip } from '@vkontakte/vkui';

class GetOneAffiliateAddress extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            AddressN:''
        }
    }

    getAddressTitle(){
        return(
            <span>
               {this.props.AddresInfo.title}
            </span>
        );
    }

    getAddressFull(){
        return(
            <span style={{"color":"#ffae6b"}} dangerouslySetInnerHTML={{__html: this.props.AddresInfo.operating_mode}}>

            </span>
        );
    }

    defaultCheckedR(e,n){
        if(e === n){
            return (
                <Radio
                    name="AffiliateAddress"
                    value={this.props.AddresInfo.id}
                    description={this.getAddressFull()}
                    defaultChecked onClick={() => {
                        this.props.setAffiliateDefault(n);
                    ;this.props.setAddressInfoUsers(n);
                    }
                    }
                >{
                    this.getAddressTitle()
                }
                </Radio>
            );
        }else{
            return(
                <Radio
                    name="AffiliateAddress"
                    value={this.props.AddresInfo.id}
                    description={this.getAddressFull()}
                    onClick={() => {
                        this.props.setAffiliateDefault(n)}
                    }
                >{
                    this.getAddressTitle()
                }
                </Radio>
            );
        }
    }

    render(){

        return(
            this.defaultCheckedR(this.props.AffiliateDefault,this.props.NumberAddress)
        );
    };
}

export default GetOneAffiliateAddress;