import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import {Div,Radio, Input,FormLayout,Group,Button, List, Cell, Snackbar,Avatar,Textarea, View, Panel, PanelHeader, CellButton, HeaderButton, platform, IOS, Tooltip, Slider, Select, Placeholder } from '@vkontakte/vkui';
import axios from "axios";
import Icon56ErrorOutline from '@vkontakte/icons/dist/56/error_outline';
import NewOrderServer from './NewOrderServer';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';
import Icon24MoneyCircle from '@vkontakte/icons/dist/24/money_circle';
import Icon24Info from '@vkontakte/icons/dist/24/info';
import Icon24Place from '@vkontakte/icons/dist/24/place';
import Icon24Flash from '@vkontakte/icons/dist/24/flash';

class PaymentsPage extends React.Component{
    constructor (props)
    {
        super(props);
        this.state = {
            SalePoints:this.props.SalePoints,
            value2:0,
            statusNewOrder:'11'
        }

        this.props.getReloadInfODeliveryPay();
    }

    setPointsDefaultsSale(SalePoints){
        SalePoints = SalePoints*1;
        this.setState({SalePoints});
        this.props.setStates({SalePoints:SalePoints});
    }

    options () {
        let options = [];
        for (let i = 0; i <= this.props.userSystemInfo.dataUser.point*1; i += 1) {
            options.push(<option value={`${i}`} key={`${i}`}>{i}</option>)
        }
        return options;
    }

    getCountsTimeBonus(){
        var points = this.props.userSystemInfo.dataUser.point*1;
        if(points >0){
            return(
                <div>
                        <Div>   : <b>{points}</b></Div>
                    <FormLayout>
                        <Slider
                            step={1}
                            min={0}
                            max={points}
                            value={Number(this.state.SalePoints)}
                            onChange={(SalePoints)=> this.setPointsDefaultsSale(SalePoints)}
                            top="  "
                        />
                        <Select onChange={e => this.setPointsDefaultsSale(e.target.value)} value={String(this.state.SalePoints)}>
                            {this.options()}
                        </Select>

                    <div style={{display: 'none',justifyContent:'center'}}>
                        <Button size="l" stretched style={{marginRight: 10}}> -</Button>
                        <Button size="l" stretched style={{marginLeft: 10}}> </Button>
                    </div>
                    </FormLayout>
                </div>);
        }else{
            return(<Cell className="IconColorOrange" before={<Icon24Coins />}>     <b>0</b> </Cell>);
        }
    }

    getSumCartItogo(){
        var sum = this.props.sumCart-this.props.SalePoints;
        if(this.props.DataDeliveryInfo.DeliveryPriceItog) {
            sum = sum + (this.props.DataDeliveryInfo.priceDelivery * 1);
        }
        if(sum<0){sum = 0;}
        return (sum);
    }


    checkMinSummaCart(){
        if(this.props.DataDeliveryInfo.errorOrderFinal){
            return(
                <Placeholder
                    icon={<Icon56ErrorOutline />}
                    action={false}

                >
                    <h2>   {this.props.DataDeliveryInfo.minSumOrder} </h2>
                    <p>     ,     ,  </p>
                </Placeholder>
            );
        }else{
            return(
                this.checkTypeDiliveryInfo()
            );
        }
    }

    PriceInfoVisual(){
        if(this.props.DataDeliveryInfo.DeliveryPriceItog){
            return(
                <Cell before={<Icon24Info />}>  : <b>{this.props.DataDeliveryInfo.priceDelivery} </b></Cell>
            );
        }else{
            return('');
        }
    }

    getAddressDelivery(e){
        if(Object.keys(this.props.DataDeliveryInfo.listAddressGlobal).length>0){
            var listAddressU = Object.keys(this.props.DataDeliveryInfo.listAddressGlobal);
            // console.log(listAddressU,"  ");

            // console.log('    ',this.state.listAddress[listAddressU[0]].diliveryData.priceDilivery);

            var DataSetAddress = this.props.DataDeliveryInfo.listAddressGlobal[listAddressU[e]];
        }else{
            var DataSetAddress = {id:"error"}
        }

        return DataSetAddress;

    }

    getAddressAffiliate(e){
        if(Object.keys(this.props.DataDeliveryInfo.listAffiliateGlobal).length>0){
            var listAddressU = Object.keys(this.props.DataDeliveryInfo.listAffiliateGlobal);
            // console.log(listAddressU,"  ");

            // console.log('    ',this.state.listAddress[listAddressU[0]].diliveryData.priceDilivery);

            var DataSetAddress = this.props.DataDeliveryInfo.listAffiliateGlobal[listAddressU[e]];
        }else{
            var DataSetAddress = {id:"error"}
        }

        return DataSetAddress;

    }

    getDataOrder(){
        if(this.props.DataDeliveryInfo.typeOrderDate === true) {
            var checkPreorderActive = false;
        }else{
            var checkPreorderActive = true;
        }
        var DataOrder = {};

        var DataAddressDelivery = this.getAddressDelivery(this.props.DataDeliveryInfo.addressDefault);
        var DataAddressAffiliate = this.getAddressAffiliate(this.props.DataDeliveryInfo.AffiliateDefault);

        DataOrder.idUserVK = this.props.userInfo.id;
        DataOrder.typeDelivery = this.props.DataDeliveryInfo.checkedTypeDilivery;
        DataOrder.AddressDelivery = {"Info":DataAddressDelivery,"idAddress":DataAddressDelivery.id};
        DataOrder.idAffiliatePickup = {"Info":DataAddressAffiliate,"idAffiliate":DataAddressAffiliate.id};
        DataOrder.PointsSale = this.props.SalePoints;
        DataOrder.DeliverySum = this.props.DataDeliveryInfo.DeliveryPriceItog ? this.props.DataDeliveryInfo.priceDelivery : 0;
        DataOrder.Comments = this.props.DataDeliveryInfo.comments;
        DataOrder.DopInfo = this.props.DataDeliveryInfo.commentary;
        DataOrder.typePay = this.props.DataDeliveryInfo.typePay;
        DataOrder.countPerson = this.props.DataDeliveryInfo.countPerson;
        DataOrder.cart = this.props.DataDeliveryInfo.cart;
        DataOrder.DopProductUserSet = this.props.DataDeliveryInfo.DopProductUserSet;
        DataOrder.Preorder = {"active":checkPreorderActive,"datePreorder":this.props.DataDeliveryInfo.SelectDatePreorder+'T'+this.props.DataDeliveryInfo.SelectTimePreorder};
        return DataOrder;
    }

    getMountsDate(a,b = null){
        if(b){
            a = a+1;
            if(a<10){return '0'+a;}
        }else {
            switch (a) {
                case 0:
                    return '';
                    break;
                case 1 :
                    return '';
                    break;
                case 2 :
                    return '';
                    break;
                case 3 :
                    return '';
                    break;
                case 4 :
                    return '';
                    break;
                case 5 :
                    return '';
                    break;
                case 6 :
                    return '';
                    break;
                case 7 :
                    return '';
                    break;
                case 8 :
                    return '';
                    break;
                case 9 :
                    return '';
                    break;
                case 10 :
                    return '';
                    break;
                case 11 :
                    return '';
                    break;
                default:
                    return a;
            }
        }
    }

    returnPlaceholderPreorder(a){
        switch(a){
            case true:
                var dateObj = new Date(this.props.DataDeliveryInfo.SelectDatePreorder);
                //var formated_date = dateObj.format("dd.mm.YYYY");


                return(<div>
                        <Group title="  ">
                            {this.getCountsTimeBonus()}

                        </Group>
                        <Group title="   ">
                            <List>
                                <Cell
                                    before={<Icon24Place />}
                                    description={<span>
                                      <i>{this.props.DataDeliveryInfo.addressOrderInfoUser}</i>
                                  </span>}
                                    size="l">
                                    {this.props.checkedTypeDilivery === 0 ? "  " : "   "}
                                </Cell>
                                <Cell
                                before={<Icon24Flash />}
                                description={this.props.DataDeliveryInfo.typeOrderDate ? <i>   </i> :
                                    <i> {dateObj.getDate()+' '+this.getMountsDate(dateObj.getMonth(),false)+' '+dateObj.getUTCFullYear()+'.  '+this.props.DataDeliveryInfo.SelectTimePreorder}</i>
                                }
                                >
                                      
                                </Cell>
                                <Cell before={<Icon24Coins />}>  : <b>{this.props.SalePoints}</b></Cell>
                                {this.PriceInfoVisual()}
                                <Cell before={<Icon24MoneyCircle />}>  : <b className="OrangeColor">{this.getSumCartItogo()} </b></Cell>
                            </List>
                        </Group>
                        <Group title=" ">
                            <List>
                                <Cell>
                                    <Radio name="typePayments" value="1"
                                           description="   "
                                           defaultChecked={this.props.DataDeliveryInfo.typePay === 1 ? true : false}
                                           onClick={() => {this.props.setStates({typePay:1})}}></Radio>
                                    <Radio name="typePayments" value='2' description="  ,  "
                                           defaultChecked={this.props.DataDeliveryInfo.typePay === 2 ? true : false}
                                           onClick={() => {this.props.setStates({typePay:2})}}></Radio>

                                </Cell>
                                <Cell>
                                    <Div>
                                        <NewOrderServer setStates={this.setState.bind(this)}
                                                        userInfo={this.props.userInfo}
                                                        DataOrderInfo={this.getDataOrder()}
                                                        getPageOkOrderSet={this.props.setStates.bind(this)}
                                                        setOkNewOrderView={this.props.setOkNewOrderView.bind(this)}
                                        />
                                    </Div>
                                </Cell>
                            </List>
                        </Group>
                    </div>
                );
                break;
            default:
                return(
                    <Placeholder
                        icon={<Icon56ErrorOutline />}
                        action={<Button size="l" level="tertiary" onClick={()=>{this.props.action({activePanel: "dilivery"});}}>  </Button>}

                    >
                        <h2>     </h2>
                        <p>,    ""     .</p>
                    </Placeholder>
                );
        }
    }

    checkPreorderType(){
        if(this.props.DataDeliveryInfo.typeOrderDate === false) {
            if(this.props.DataDeliveryInfo.SelectDatePreorder === null){
                return this.returnPlaceholderPreorder(false);
            }else{
                if(this.props.DataDeliveryInfo.SelectTimePreorder === null){
                    return this.returnPlaceholderPreorder(false);
                }else{
                    return this.returnPlaceholderPreorder(true);
                }
            }

        }else{
            return this.returnPlaceholderPreorder(true);
        }
    }


    checkTypeDiliveryInfo(){
        if(this.props.checkedTypeDilivery === 0 && this.props.addressDefault === null){
            return(
                <Placeholder
                    icon={<Icon56ErrorOutline />}
                    action={<Button size="l" level="tertiary" onClick={()=>{this.props.action({activePanel: "dilivery"});}}> </Button>}

                >
                <h2>    </h2>
                    <p>,    ""    ,    ,      .</p>
                </Placeholder>

            );
        }else{
            return (this.checkPreorderType());
        }
    }

    render(){
        return(
            this.checkMinSummaCart()
        );
    }
}

export default PaymentsPage;