import React from 'react';
import connect from '@vkontakte/vk-connect';
import {Div, Group, List,Cell, Switch} from '@vkontakte/vkui/';
import Icon24Place from '@vkontakte/icons/dist/24/place';
import Icon24Recent from '@vkontakte/icons/dist/24/recent';


class NoPreorder extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loadBlockOrder:false
        }
    }

    componentDidMount() {
        this.setState({loadBlockOrder:true});
    }

    getBlocknoPreorder(){
        return(
                <Cell
                    before={<Icon24Recent />}
                    style={{paddingBottom:"10px"}}
                >
                    {
                        this.state.loadBlockOrder ?
                            <div>{this.props.typeDelivery ? "   " : "   "}</div>
                            :
                            "   "
                    }
                </Cell>
        );
    }

    render(){
        return(
            this.getBlocknoPreorder()
        );
    }
}
export default NoPreorder;
