import React, { useState, useEffect } from 'react';
import connect from '@vkontakte/vk-connect';
import {Placeholder, Div,Input,FormLayout,FormLayoutGroup,Button,Snackbar,Avatar,Textarea, View, Panel, PanelHeader, Group, CellButton, HeaderButton, platform, IOS, Tooltip } from '@vkontakte/vkui';
import axios from "axios";
import Icon16Clear from '@vkontakte/icons/dist/16/clear';
import DiliveryAddress from "./DiliveryAddress";
import AffiliateList from "./AffiliateList";
import Icon56ErrorOutline from '@vkontakte/icons/dist/56/error_outline';
import Preorder from "../preorder";
import NoPreorder from "../noPreorder";


const orangeBackground = {
    backgroundImage: 'linear-gradient(135deg, #ffb73d, #ffa000)'
};

const blueBackground = {
    backgroundColor: '#ffffff'
};

class Dilivery extends React.Component{
    constructor (props)
    {
        super(props);
        this.state = {
            UserCard:{phone:"",email:""},
            CheckSMSCode:false,
            phoneValue:"",
            codeValue:"",
            resultVerificationCode:true,
            tooltip: true,
            snackbar: null,
            showBTNNewAddress:false,
            showFormNewAddress:false
        };

        this.openBase = this.openBase.bind(this);
    }

    componentDidMount() {
        //this.openBase();

        async function getPhoneUserVK(setState){
            connect.sendPromise("VKWebAppGetPhoneNumber",{"sign":"T3in3QPDby8GsRNAfsEnEoYmVt7Se2dy0XUOhnCSlSw"})
                .then((data ) => {
                    //console.log(data," ");
                    setState({ UserCard:{phone: data.phone_number} });
                })
                .catch((err) => {
                    console.log("ERROR: ------  ",err);
                });
        }

        //console.log(" ");
        {this.props.userSystemInfo.error === true && getPhoneUserVK(this.setState.bind(this));}
    }


    openBase (e = null) {
        if (this.state.snackbar) return;
        this.setState({ snackbar:
                <Snackbar
                    layout="vertical"
                    onClose={() => this.setState({ snackbar: null })}
                    before={<Avatar size={24} style={blueBackground}><Icon16Clear fill="#ff0000" width={16} height={16} /></Avatar>}
                >
                    {e===null && <span>    ,  !</span>}
                    {e!==null && e}
                </Snackbar>
        });
    }


    setVerificationPhone(){
        //console.log('   ');
        axios('https://-./api/VerificationPhone&apiKey='+global.ApiKey+'',
            {
                method: 'POST',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                data: {
                    phoneVerification: this.state.UserCard.phone,
                },
                responseType:'text'
            })
            .then(({ data }) => {
                if("error" in data){
                    console.log(data.error,'|',data.errorInfo);
                }else{
                    if(data.status === "OK_SMS"){
                        //console.log(' ,     ');
                        this.setState({CheckSMSCode:true});
                    }else{
                        //console.log(' ',data);
                        this.openBase('    !');
                    }
                }
            })
            .catch(({err}) => {
                console.log("ERROR: --- ---  ",err);
            });
    }

    checkVerificationCode(){
        //console.log('    ',this.props.userInfo.id);
        axios('https://-./api/VerificationCode&apiKey='+global.ApiKey+'',
            {
                method: 'POST',
                headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
                data: {
                    phoneVerification: this.state.UserCard.phone,
                    VerificationCode: this.state.codeValue,
                    idUserVK:this.props.userInfo.id
                },
                responseType:'text'
            })
            .then(({ data }) => {
                if("error" in data){
                    console.log(data.error,'|',data.errorInfo);
                }else{
                    if(data.status === "OK_CODE_V"){
                        //console.log(' ,   ,  ');
                        this.props.getUserSystemInfo(this.props.userInfo.id);
                        //this.setState({CheckSMSCode:true});
                    }else{
                        //console.log(' ',data);
                        this.openBase();
                    }
                }
            })
            .catch((err) => {
                console.log("ERROR: --- ---  ",err);
            });
    }

    getValueInputPhone(event){
        this.setState({UserCard:{phone: event.target.value}});
        //console.log(this.state.UserCard.phone);
    }

    getValueInputCode(event){
        this.setState({codeValue: event.target.value});
    }

    getCommentsData(e){
        this.props.setComment(e.target.value);
    }

    getDataCountPerson(types){
        var values_count = this.props.countPerson;
        switch(types){
            case '-': values_count -= 1; break;
            case '+': values_count += 1; break;
            default: console.log(' ');
        }

        if(values_count<=0){ values_count = 1;}
        if(values_count === ''){ values_count = 1;}
        if(values_count >9){ values_count = 9;}

        this.props.setStates({countPerson: values_count});
    }

    getCountPersonInput(){
        return "";
        // return (
        //     <Group title="- ">
        //         <Div style={{display: 'flex',justifyContent:'center'}}>
        //             <Button size="l" stretched style={{marginRight: 10,maxWidth:50,background:"#ff7300"}} onClick={this.getDataCountPerson.bind(this,'-')}>-</Button>
        //             <Input type="number" style={{maxWidth:80,textAlign:"center"}} value={this.props.countPerson} readOnly="readOnly"/>
        //             <Button size="l" stretched style={{marginLeft: 10,maxWidth:50,background:"#ff7300"}} onClick={this.getDataCountPerson.bind(this,'+')}>+</Button>
        //         </Div>
        //     </Group>
        // );
    }

    getInfoUserNoneAuth(){
        return(<Group>
            <FormLayout>
                {this.state.CheckSMSCode === true && <FormLayoutGroup top="    ">

                    <Input type="number" placeholder="1234" onChange={this.getValueInputCode.bind(this)} />


                    <Button level="commerce" style={{"background":"#ff7300"}}
                            onClick={()=> {
                                //console.log(this.state.codeValue);
                                this.checkVerificationCode()
                                }
                            }
                    >
                    </Button>
                    <Button level="secondary" style={{"float":"right"}} onClick={() => {this.setState({CheckSMSCode:false})}}> </Button>
                </FormLayoutGroup>}

                {this.state.CheckSMSCode === false && <FormLayoutGroup top="  ">
                    <Input type="number" placeholder="79876543210" defaultValue={this.state.UserCard.phone} onChange={this.getValueInputPhone.bind(this)}/>
                    <Button level="commerce" style={{"background":"#ff7300"}}
                            onClick={
                                () => {
                                    this.setVerificationPhone();
                                    //console.log(this.state.UserCard.phone)
                                }
                            }
                    >
                         
                    </Button>
                    <Button onClick={() => {this.setState({CheckSMSCode:true})}} level="secondary" style={{"float":"right"}}>  </Button>
                </FormLayoutGroup>}
            </FormLayout>
            {this.state.snackbar}
        </Group>);
    }

    getButtonTypeDilivery(){
        return(<Div style={{display: 'flex',"paddingBottom":"0"}}>
            <Button size="l" stretched style={{marginRight: 8}} level={this.props.checkedTypeDilivery === 1 ? "secondary" : "commerce"} onClick={()=>{this.props.setTypeDilivery(0)}}></Button>
            <Button size = "l" stretched level={this.props.checkedTypeDilivery === 0 ? "secondary" : "commerce"}  onClick={()=>{this.props.setTypeDilivery(1)}}></Button>
            </Div>
        );
    }

    getButtonTypeOrder(){
        return(<Div style={{display: 'flex',"paddingBottom":"20","paddingTop":"0"}}>
                <Button size="l" stretched style={{marginRight: 8}}
                        level={this.props.DataDeliveryInfo.typeOrderDate ? "commerce" : "secondary"}
                        onClick={()=>{this.props.DataDeliveryInfo.setTypeOrderDate(true);}}
                >
                    
                </Button>
                <Button size = "l" stretched
                        level={this.props.DataDeliveryInfo.typeOrderDate ? "secondary" : "commerce"}
                        onClick={()=>{this.props.DataDeliveryInfo.setTypeOrderDate(false);}}
                >
                     
                </Button>
            </Div>
        );
    }


    getPreorderBlock(){
        return(
            <Group
                title="  "
            >
                {this.getButtonTypeOrder()}
                {
                    this.props.DataDeliveryInfo.typeOrderDate
                        ?
                        <NoPreorder
                            typeDelivery={this.props.DataDeliveryInfo.checkedTypeDilivery}
                        />
                        :
                        <Preorder
                            DataInfos={this.props.DataDeliveryInfo}
                            setStates={this.props.setStates.bind(this)}
                            key="Preorders"
                        />
                }
            </Group>
        );
    }

    getInfoUserAuth(){
        //console.log('   ',this.props.addressDefault)
        return(
            <div>
                {this.getButtonTypeDilivery()}
                {this.props.checkedTypeDilivery === 0 ?
                    this.getListAddress()
                    :
                    this.getListAffiliate()
                }
                {this.getPreorderBlock()}
                {this.getCountPersonInput()}
                {this.getCommentsTextarea()}
            </div>
        );
    }

    getNoneCartProduct(){
        return(
            <Placeholder
                icon={<Icon56ErrorOutline />}
                action={false}

            >
                <h2>   </h2>
                <p>  ,    1   !</p>
            </Placeholder>
        );
    }


    getListAddress(){
        //console.log(this.state.showBTNNewAddress);
        return(
            <Group
                className="GroupBlockListAddress"
                title=" "
                   description={this.state.showBTNNewAddress ?
                       <Button
                           size="l"
                           level="secondary"
                           style={{ background: '#FF7300',color:"#FFFFFF" }}
                           // onClick={()=>{this.setState({showFormNewAddress:true})}}
                           onClick={()=>{
                               this.props.showModal('MODAL_NEWADDRESS');
                           }}
                       > </Button>
                       :
                       ""}
            >
                <DiliveryAddress
                    userInfo={this.props.userInfo}
                    addressDefault={this.props.addressDefault}
                    setAddressDefault={this.props.setAddressDefault.bind(this)}
                    setPopout={this.props.setPopout.bind(this)}
                    setShowBTNNewAddress={this.setState.bind(this)}
                    showFormNewAddress={this.state.showFormNewAddress}
                    setStates={this.props.setStates.bind(this)}
                    setDeliveryDataItogs={this.props.setDeliveryDataItogs.bind(this)}

                    showModals={this.props.showModal.bind(this)}
                    hideModals={this.props.hideModals.bind(this)}
                />
            </Group>
        );
    }

    getListAffiliate(){
        return (
            <Group title=" ">
            <AffiliateList
                userInfo={this.props.userInfo}
                AffiliateDefault={this.props.AffiliateDefault}
                setAffiliateDefault={this.props.setAffiliateDefault.bind(this)}
                getDeliveryDataPriceItogAffiliate={this.props.getDeliveryDataPriceItogAffiliate.bind(this)}
                setStates={this.props.setStates.bind(this)}
            />
            </Group>
        );
    }

    getCommentsTextarea () {
        return (
            <Group title="  ">
                <Div>
                <Textarea value={this.props.commentary} onChange={this.getCommentsData.bind(this)} placeholder=" "/>
                </Div>
            </Group>
        );
    }

    getOkListAddress(Param){
        return(
            Param <=0 ? this.getNoneCartProduct() : this.getInfoUserAuth()
        )
    }

    render(){

        return(
            <div>
                {this.props.userSystemInfo.error === true &&
                    this.getInfoUserNoneAuth()
                }

                {this.props.userSystemInfo.error === false &&
                    this.getOkListAddress(this.props.ProductList.length)
                }

            </div>
        );
    }

}


export default Dilivery;