import React, {useState} from 'react';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import {Div,PanelHeader,HeaderButton, IOS,platform,Placeholder} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import ProductList from "./catalog/ProductList";
import Icon28Search from '@vkontakte/icons/dist/28/search';
import ym from 'react-yandex-metrika';

require('./global/index.js');

class SearchResultProductBlock extends React.Component {
    constructor (props) {
        super(props);

        this.state = {
            arrayProduct:this.props.arrayProduct,
            settingsFilterSearch:{
                typeSearch: global.settingsFilterSearch.typeSearch
            }
            ,
            search: global.searchProduct
        }
    }

    hndlClick = (aP) => {
        //console.log('--- ', '   '+aP);
        this.setState({ activePanel: aP });
        //global.settingsFilterSearch.typeProduct = false;
    };

    setProductModal = (aP) => {
        // console.log('--- ', '    '+aP+' - - - -XTTT');
        this.setState({ productModal: aP })
    };

    setActiveCategory = (aP) => {
        // console.log('--- ', '  '+aP);
        this.setState({ ActiveCategory: aP })
    };

    getCountButton(typeProduct,counts,setCart){
        const Buttons =
            <div className="CounterInCart">
                <div className="minus" onClick={() => {setCart(typeProduct,'-');}}>
                    -
                </div>
                {counts}
                <div className="pluse" onClick={() => {setCart(typeProduct);}}>
                    +
                </div>
            </div>;

        return Buttons;
    }


    getButtonInCartProduct(typeProduct,setCart){
        //console.log(this.props.CartProducts,'---0---');
        if(!!this.props.CartProducts){
            var ButOk = false;
            var id_CartPr = '';
            this.props.CartProducts.map((data,key)=>{
                if(data.id_Product === typeProduct.id_Product & data.type_product === typeProduct.type_product){
                    ButOk = true;
                    id_CartPr = key;
                }
            });

            if(ButOk === true){
                //console.log('   ');
                return  this.getCountButton(typeProduct,this.props.CartProducts[id_CartPr]['count'],setCart);
            }else{
                //console.log('   ');
                return this.getInCarts(typeProduct,setCart);
            }
        }else{
            //console.log('   ,   ');
            return this.getInCarts(typeProduct,setCart);
        }


    }

    getInCarts(typeProduct,setCart){
        // console.log(setCart);
        const Buttons =
            <button onClick={() => {
                setCart(typeProduct);
                try {
                    if (global.Metrika.Markers[1].active == '1') {
                        ym('reachGoal', global.Metrika.Markers[1].marker);
                    }else{
                        //   console.log(' ');
                    }
                }catch (e) {
                    // console.log(e,global.Metrika);
                }
            }}>
                 
            </button>;

        return Buttons;
    }

    componentDidMount() {
        console.log(this.state.arrayProduct);
    }

    getArrayCategoryProduct(){
        const ArCat = Object.keys(this.state.arrayProduct);
        const ResultProductList =  ArCat.map((data,key)=>{
            //console.log(data,key, '');

            var ResultCatsSearch =
                <ProductList
                    onClickProduct={this.hndlClick.bind(this,'Panels_Product')}
                    productsLists={this.state.arrayProduct[data].ProductList}
                    key={this.state.arrayProduct[data].id_Category}
                    idCategorys={data}
                    nameCategory="sets"
                    setCart={this.props.setCart.bind(this)}
                    getButtonInCartProduct={this.getButtonInCartProduct.bind(this)}
                    setProductModal={this.setProductModal.bind(this)}
                    searchProduct={this.props.varSearch}

                    showModal={this.props.showModal.bind(this)}
                    hideModals={this.props.hideModals.bind(this,null)}

                    clrStatus={false}
                />;

            return (ResultCatsSearch);

        });
        return ResultProductList;
    }

    getProductSearchResult(OPT) {

        return (
            this.getArrayCategoryProduct()
        );
    }

    getStatusSearch(){
        var statusText = false;
        var statusFiltes = false;

        this.props.varSearch === '' || this.props.varSearch === undefined ? statusText = false : statusText = true;
        global.settingsFilterSearch.typeProduct === false ? statusFiltes = false : statusFiltes = true;

        if(statusFiltes === true || statusText === true){
            //console.log(statusFiltes,statusText);
            return true;
        }else{
            //console.log(statusFiltes,statusText);
            return false;
        }

    }


    render () {

        const osname = platform();




        return (<Div style={{background:"#FFFFFF",paddingTop:"60px"}}>
            {
                this.getStatusSearch() === false ?
                    <Placeholder
                        icon={<Icon28Search />}
                        header=" "
                    >
                             
                    </Placeholder>
                    :
                    this.getProductSearchResult()
            }
        </Div>);
    }
}



export default SearchResultProductBlock;